import pdfMake from 'pdfmake/build/pdfmake'
import pdfFonts from 'pdfmake/build/vfs_fonts'

// pdfMake에 폰트 추가
pdfMake.vfs = pdfFonts.pdfMake.vfs

// 사용자 정의 폰트 정의
pdfMake.fonts = {
  NotoSansCJKkr: {
    normal: 'NotoSansKR-Regular.ttf',
    bold: 'NotoSansKR-Bold.ttf',
    italics: 'NotoSansKR-Regular.ttf',
    bolditalics: 'NotoSansKR-Bold.ttf'
  }
}

// 내보낼 헤더를 가져오는 함수
const getHeaderToExport = (gridApi) => {
  const columns = gridApi.getAllDisplayedColumns().filter(col => col.getColDef().field !== 'detail_button')
  return columns.map((column) => {
    const colDef = column.getColDef()
    const { field } = colDef
    const sort = column.getSort()
    let headerName = colDef.headerName ?? field

    // 특정 필드의 헤더 이름 변경
    switch (field) {
      case 'payment_total':
        headerName = '결재금액\n(부가세 포함)'
        break
      // 다른 필드에 대한 추가 설정이 필요하면 여기에 추가
    }

    const headerCell = {
      text: headerName + (sort ? ` (${sort})` : ''),
      bold: true,
      fontSize: 10,
      alignment: 'center',
      margin: [0, 12, 0, 0]
    }
    return headerCell
  })
}

// 천단위 콤마
const formatNumberWithCommas = (value) => {
  return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
}

// 내보낼 행을 가져오는 함수
const getRowsToExport = (gridApi) => {
  const columns = gridApi.getAllDisplayedColumns().filter(col => col.getColDef().field !== 'detail_button')
  const getCellToExport = (column, node) => {
    let value = gridApi.getValue(column, node) ?? ''
    if (column.getColDef().field === 'odr_sts') {
      switch (value) {
        case 0: value = '주문취소'; break
        case 1: value = '취소완료'; break
        case 2: value = '입금완료'; break
        case 3: value = '배송완료'; break
        case 4: value = '계산서발행완료'; break
      }
    }
    // 천단위 콤마
    if (['ods_prc', 'ods_qty', 'ods_tot', 'payment_total'].includes(column.getColDef().field)) {
      value = formatNumberWithCommas(value)
    }
    return {
      text: value,
      fontSize: 9,
      ...column.getColDef().cellStyle
    }
  }
  const rowsToExport = []
  gridApi.forEachNodeAfterFilterAndSort((node) => {
    const rowToExport = columns.map((column) => getCellToExport(column, node))
    rowsToExport.push(rowToExport)
  })
  return rowsToExport
}

// PDF 레이아웃 및 스타일 정의
const HEADER_ROW_COLOR = '#f8f8f8'
const EVEN_ROW_COLOR = '#fcfcfc'
const ODD_ROW_COLOR = '#fff'
const PDF_INNER_BORDER_COLOR = '#dde2eb'
const PDF_OUTER_BORDER_COLOR = '#babfc7'

const createLayout = (numberOfHeaderRows) => ({
  fillColor: (rowIndex) => {
    if (rowIndex < numberOfHeaderRows) {
      return HEADER_ROW_COLOR
    }
    return rowIndex % 2 === 0 ? EVEN_ROW_COLOR : ODD_ROW_COLOR
  },
  vLineWidth: (rowIndex, node) =>
    rowIndex === 0 || rowIndex === node.table.widths.length ? 1 : 0,
  hLineColor: (rowIndex, node) =>
    rowIndex === 0 || rowIndex === node.table.body.length
      ? PDF_OUTER_BORDER_COLOR
      : PDF_INNER_BORDER_COLOR,
  vLineColor: (rowIndex, node) =>
    rowIndex === 0 || rowIndex === node.table.widths.length
      ? PDF_OUTER_BORDER_COLOR
      : PDF_INNER_BORDER_COLOR
})

// pdfMake를 위한 문서 설정 함수
const getDocument = (gridApi, startDate, endDate) => {
  const columns = gridApi.getAllDisplayedColumns().filter(col => col.getColDef().field !== 'detail_button')
  const headerRow = getHeaderToExport(gridApi)
  const rows = getRowsToExport(gridApi)

  return {
    pageOrientation: 'landscape',
    content: [
      {
        text: `조회 기간: ${startDate} ~ ${endDate}`,
        alignment: 'center',
        margin: [0, 0, 0, 10],
        fontSize: 12
      },
      {
        table: {
          headerRows: 1,
          widths: columns.map(() => `${100 / columns.length}%`),
          body: [headerRow, ...rows],
          heights: (rowIndex) => (rowIndex === 0 ? 40 : 15)
        },
        layout: createLayout(1)
      }
    ],
    pageMargins: [10, 10, 10, 10],
    defaultStyle: {
      font: 'NotoSansCJKkr'
    }
  }
}

// PDF 생성 및 내보내기 함수
export const exportToPDF = (gridApi, startDate, endDate) => {
  const doc = getDocument(gridApi, startDate, endDate)
  pdfMake.createPdf(doc).download()
}
