<template>
  <div class="flex justify-center items-center h-screen bg-gray-200">
    <div class="w-96 p-8 bg-white rounded-lg shadow">
      <h1 class="text-3xl font-semibold mb-8">MAIN LOGIN</h1>
      <form @submit.prevent="submit()">
        <input type="text" class="border border-gray-300 w-full h-12 mb-4 px-3 rounded" placeholder="아이디"
          v-model="this.state.form.userId" ref="cursor" autofocus />
        <input type="password" class="border border-gray-300 w-full h-12 mb-4 px-3 rounded" placeholder="비밀번호"
          v-model="this.state.form.password" />
        <button type="submit" class="w-full h-12 bg-blue-500 text-white font-semibold rounded hover:bg-blue-600">
          로그인
        </button>
      </form>
      <br>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import store from '@/store'
import { reactive } from 'vue'
import router from '@/router'

export default {
  name: 'ScmLoginView',
  setup () {
    const state = reactive({
      form: {
        userId: '',
        password: '',
        usr_name: '',
        img_logo: ''
      }
    })

    const submit = () => {
      console.info('현재 상태:', state.form)
      axios.post('/api/scmusr/userCheck', state.form).then((res) => {
        if (res.data.usr_id) {
          store.commit('setUserInfo', {
            usrId: res.data.usr_id,
            usrName: res.data.usr_name,
            usrCom: res.data.usr_com,
            imgLogo: res.data.img_logo
          })
          login()
        } else {
          alert('사용자 아이디와 비밀번호를 확인하세요')
        }
      }).catch(() => {
        window.alert('로그인 정보가 존재하지 않습니다.')
      })
    }

    function login () {
      router.push({ path: '/contract/contractHome' })
    }

    return {
      state,
      submit
    }
  }
}
</script>

