<template>
  <div class="content">
    <!-- 페이지 제목 -->
    <h1 style="margin: 15px -25px -35px 25px;">
      <span class="text-2xl font-weight: 100;"><i class="fa-solid fa-bars"></i> 주문하기 > </span>
      <span class="text-2xl font-bold">품목선택</span>
    </h1>

    <!-- 그리드 호출 부분 -->
    <div class="flex mx-4 my-20 h-full">
      <!-- 대분류 그리드 -->
      <div v-if="searchTarget !== 'all'" class="flex" style="width: 140px;">
        <ag-grid-vue class="ag-theme-alpine mx-1" style="width: 100%; height:650px;"
          :columnDefs="columnDefs.value"
          :rowData="rowData.value"
          :defaultColDef="defaultColDef"
          suppressRowTransform
          rowSelection="single"
          animateRows
          @cell-clicked="cellWasClicked"
          @grid-ready="onGridReady">
        </ag-grid-vue>
      </div>

      <!-- 중분류 그리드 -->
      <div v-if="searchTarget !== 'all'" class="flex" style="width: 150px;">
        <ag-grid-vue class="ag-theme-alpine mx-1" style="width: 100%; height:650px;"
          :columnDefs="columnDefs1.value"
          :rowData="rowData1.value"
          :defaultColDef="defaultColDef"
          suppressRowTransform
          rowSelection="single"
          animateRows
          @cell-clicked="cellWasClicked1"
          @grid-ready="onGridReady1">
        </ag-grid-vue>
      </div>

      <!-- 검색 및 그리드 -->
      <div class="example-header">
        <!-- 검색 대상 선택 콤보박스 -->
        <select v-model="searchTarget"
          class="border border-gray-300 rounded px-2 py-1 text-gray-700 leading-tight focus:outline-none focus:border-blue-500 mr-2">
          <option value="all">전체검색</option>
          <option value="subCategory">소분류검색</option>
        </select>

        <!-- 검색어 입력 -->
        <input type="text"
          class="border border-gray-300 rounded px-2 py-1 text-gray-700 leading-tight focus:outline-none focus:border-blue-500 mr-2 w-1/4"
          id="filter-text-box" placeholder="검색어를 입력해주세요" v-model="searchKeyword" @keyup.enter="search">

        <!-- 검색 버튼 -->
        <button class="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-5 rounded mr-2"
          @click="search">검색</button>

        <!-- 일괄 담기 버튼 -->
        <button class="bg-logout_btn text-white w-20 h-8 rounded-lg noto_santos_b font-medium_bold"
          style="margin-left: 53%;" @click="addToCart">일괄 담기</button>

        <!-- 소분류 그리드 -->
        <div ref="subCategoryGridContainer" class="flex" style="width: 1350px;">
          <ag-grid-vue class="ag-theme-alpine mx-1" style="width: 100%; height:620px;"
            :columnDefs="columnDefs2.value"
            :rowData="rowData2.value"
            :defaultColDef="defaultColDef"
            suppressRowTransform
            rowSelection="multiple"
            animateRows
            alwaysShowVerticalScroll
            suppressRowClickSelection
            @cell-clicked="cellWasClicked2"
            @grid-ready="onGridReady2"
            @row-selected="onRowSelected"
            @cell-value-changed="onCellValueChanged">
          </ag-grid-vue>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import { AgGridVue } from 'ag-grid-vue3'
import 'ag-grid-community/styles/ag-grid.css'
import 'ag-grid-community/styles/ag-theme-alpine.css'
import { reactive, onMounted, ref, watch } from 'vue'
import router from '@/router'
import axios from 'axios'
import store from '@/store'

export default {
  name: 'ContractHomeView',
  components: {
    AgGridVue
  },
  setup () {
    const rowData = reactive({ value: [] })
    const rowData1 = reactive({ value: [] })
    const rowData2 = reactive({ value: [] })
    const gridApi = ref()
    const searchKeyword = ref('')
    const searchTarget = ref('subCategory')
    const subCategoryGridContainer = ref(null)
    const usr_id = store.state.usrInfo.usrId

    const defaultColDef = {
      flex: 1,
      minWidth: 100,
      sortable: true,
      resizable: true
    }

    const columnDefs = reactive({
      value: [
        { headerName: '코드', field: 'itm_gub', hide: true, cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 50 },
        { headerName: '대분류', field: 'itm_nam', cellClass: 'textLeftCell', suppressSizeToFit: true, sortable: true, filter: false }
      ]
    })
    const columnDefs1 = reactive({
      value: [
        { headerName: '코드', field: 'knd_gub', hide: true, cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 50 },
        { headerName: '중분류', field: 'knd_nam', cellClass: 'textLeftCell', suppressSizeToFit: true, sortable: true, filter: false }
      ]
    })
    const columnDefs2 = reactive({
      value: [
        {
          headerName: '담기', field: 'ord_chk', rowSelection: 'multiple', cellEditor: 'agCheckboxCellEditor',
          suppressSizeToFit: true, maxWidth: 80,
          checkboxSelection: params => params.data.gub_nam && params.data.gub_mic > 0,
          cellRenderer: params => params.value ? 
            `<input type='checkbox' checked='${params.data.ord_chk}' disabled='${!params.data.gub_nam || params.data.gub_mic <= 0}'>` 
            : `<div></div>`
        },
        {
          headerName: '이미지', field: 'img_itm', maxWidth: 80,
          cellRenderer: params => `<img src="/img/item/${params.value}" style="width: 100px; height: 40px;">`
        },
        { headerName: '코드', field: 'gub_cod', hide: true, cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 50 },
        { headerName: '대분류명', field: 'itm_nam', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 110, hide: true },
        { headerName: '중분류명', field: 'knd_nam', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 110, hide: true },
        { headerName: '품목명', field: 'gub_nam', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 220, maxWidth: 220, sortable: true },
        { headerName: '규격', field: 'gub_spc', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 90, maxWidth: 90, sortable: true, cellStyle: { 'text-align': 'right' } },
        { headerName: '단위', field: 'gub_unt', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 90, maxWidth: 90, sortable: true, cellStyle: { 'text-align': 'right' } },
        { headerName: '단가', field: 'gub_amt', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 90, maxWidth: 90, sortable: true, cellStyle: { 'text-align': 'right' }, valueFormatter: numberWithCommas },
        { headerName: '박스당수량', field: 'gub_box', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 120, maxWidth: 120, sortable: true, cellStyle: { 'text-align': 'right' }, valueFormatter: numberWithCommas },
        { headerName: '최소주문수량', field: 'gub_moc', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 120, maxWidth: 120, sortable: true, cellStyle: { 'text-align': 'right' }, valueFormatter: numberWithCommas },
        { headerName: '재고수량', field: 'gub_mic', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 110, maxWidth: 110, sortable: true, cellStyle: { 'text-align': 'right' }, valueFormatter: numberWithCommas },
        {
          headerName: '주문수량', field: 'ord_qty', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 120, maxWidth: 120,
          sortable: true, editable: true, cellStyle: { 'text-align': 'right' },
          valueGetter: params => params.data.ord_qty || 0
        },
        {
          headerName: '주문금액', field: 'ord_amt', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 120, maxWidth: 120,
          sortable: true, editable: false, cellStyle: { 'text-align': 'right' },
          valueFormatter: numberWithCommas,
          valueGetter: params => params.data.gub_amt * params.data.ord_qty || 0
        }
      ]
    })

    // searchTarget이 변경될 때 실행되는 watcher
    watch(searchTarget, (newValue) => {
      if (newValue === 'all') {
        columnDefs2.value.forEach(colDef => {
          if (colDef.field === 'itm_nam' || colDef.field === 'knd_nam') {
            colDef.hide = false
          }
        })
        if (subCategoryGridContainer.value) {
          subCategoryGridContainer.value.style.width = '1630px'
        }
      } else {
        columnDefs2.value.forEach(colDef => {
          if (colDef.field === 'itm_nam' || colDef.field === 'knd_nam') {
            colDef.hide = true
          }
        })
        if (subCategoryGridContainer.value) {
          subCategoryGridContainer.value.style.width = '1350px'
        }
      }
      gridApi.value.setColumnDefs(columnDefs2.value)
    })

    const onGridReady = (params) => {
      params.api.addEventListener('firstDataRendered', () => {
        const firstRowNode = params.api.getDisplayedRowAtIndex(0)
        if (firstRowNode) {
          firstRowNode.setSelected(true, true)
          const firstCellId = { rowIndex: 0, colKey: 'itm_nam' }
          params.api.setFocusedCell(firstCellId.rowIndex, firstCellId.colKey)
        }
      })
      params.api.addEventListener('cellClicked', (event) => {
        const selectedRow = event.data
        loadMiddleCategories(selectedRow.itm_gub)
        const firstRowNode = rowData1.value.length > 0 ? params.api.getDisplayedRowAtIndex(0) : null
        if (firstRowNode) {
          const firstCellId = { rowIndex: 0, colKey: 'knd_nam' }
          params.api.setFocusedCell(firstCellId.rowIndex, firstCellId.colKey)
        }
      })
    }

    const onGridReady1 = (params) => {
      params.api.addEventListener('cellClicked', (event) => {
        const selectedRow = event.data
        loadMiddleCategories1(selectedRow.itm_gub, selectedRow.knd_gub)
      })
      watch(() => rowData1.value, (newValue) => {
        if (newValue.length > 0) {
          const firstRowNode = params.api.getDisplayedRowAtIndex(0)
          if (firstRowNode) {
            firstRowNode.setSelected(true, true)
            const firstCellId = { rowIndex: 0, colKey: 'knd_nam' }
            params.api.setFocusedCell(firstCellId.rowIndex, firstCellId.colKey)
          }
        }
      })
    }

    const onGridReady2 = (params) => {
      gridApi.value = params.api
      params.api.addEventListener('cellClicked', (event) => {
        const rowData = event.data
        rowData.gub_seq = rowData.gub_moc
      })
    }

    const loadData = () => {
      axios.get('/api/scmusr/gwItemRead', { params: { iuse: 'Y' } })
        .then(response => {
          rowData.value = response.data
          if (rowData.value.length > 0) {
            loadMiddleCategories(rowData.value[0].itm_gub)
          }
        })
        .catch(error => console.error('데이터 로드 중 에러 발생:', error))
    }

    const loadMiddleCategories = (itmGub) => {
      axios.get('/api/scmusr/gwKndRead', { params: { kuse: 'Y', itmGub: itmGub } })
        .then(response => {
          rowData1.value = response.data
          rowData2.value = []
          if (rowData1.value.length > 0) {
            loadMiddleCategories1(itmGub, rowData1.value[0].knd_gub)
          }
        })
        .catch(error => console.error('중분류 데이터 로드 중 에러 발생:', error))
    }
    // 소분류조회
    const loadMiddleCategories1 = (itmGub, kndGub) => {
      axios.get('/api/scmusr/gwGubRead', { params: { guse: 'Y', itmGub: itmGub, kndGub: kndGub } })
        .then(response => {
          rowData2.value = response.data
          console.log(rowData2.value)
        })
        .catch(error => console.error('세부 품목 데이터 로드 중 에러 발생:', error))
    }

    const onRowSelected = (event) => {
      const selectedNode = event.node
      const rowData = selectedNode.data
      rowData.selected = event.node.isSelected()
      if (rowData.selected) {
        rowData.ord_qty = rowData.gub_moc
      } else {
        rowData.ord_qty = 0
      }
      rowData.ord_amt = rowData.gub_amt * rowData.ord_qty
      gridApi.value.refreshCells({ columns: ['ord_qty', 'ord_amt'], rowNodes: [selectedNode] })
    }

    const onCellValueChanged = (event) => {
      if (event.column.colId === 'ord_qty') {
        const newValue = parseInt(event.newValue)
        const minValue = event.data.gub_moc
        if (newValue < minValue) {
          event.node.setDataValue('ord_qty', minValue)
          alert(`최소주문수량은 ${minValue}입니다.`)
        } else {
          event.node.setDataValue('ord_qty', newValue)
        }
        event.node.setSelected(true)
        event.node.setDataValue('ord_amt', event.data.gub_amt * newValue)
        gridApi.value.refreshCells({ columns: ['ord_amt'], rowNodes: [event.node] })
      }
    }

    const search = () => {
      if (searchTarget.value === 'all') {
        axios.get('/api/scmusr/gwGubRead', { params: { guse: 'Y', searchKeyword: searchKeyword.value } })
          .then(response => {
            const filteredRows = response.data.filter(row => row.gub_nam.includes(searchKeyword.value))
            rowData2.value = filteredRows
          })
          .catch(error => console.error('전체검색 중 에러 발생:', error))
      } else if (searchTarget.value === 'subCategory') {
        const filteredRows = rowData2.value.filter(row => row.gub_nam.includes(searchKeyword.value))
        rowData2.value = filteredRows
      }
    }

    // 장바구니 추가
    const addToCart = () => {
      const selectedProducts = rowData2.value.filter(item => item.ord_qty > 0)
      if (selectedProducts.length === 0) {
        alert('장바구니에 추가할 상품을 선택해주세요.')
        return
      }
      const productsWithUser = selectedProducts.map(product => ({
        ...product,
        usr_id: usr_id
      }))
      axios.post('/api/scmusr/updateorder', productsWithUser)
        .then(response => {
          alert('상품이 장바구니에 추가되었습니다.')
          if (confirm('품목을 담았습니다. 장바구니로 이동하시겠습니까?')) {
            router.push({ path: '/contract/ordercart' })
          }
          selectedProducts.forEach(product => { product.ord_qty = 0 })
        })
        .catch(error => console.error('장바구니 추가 중 에러 발생:', error.response.data))
    }

    function numberWithCommas (params) {
      return params.value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
    }

    onMounted(() => {
      loadData()
    })

    return {
      rowData,
      rowData1,
      rowData2,
      columnDefs,
      columnDefs1,
      columnDefs2,
      onGridReady,
      onGridReady1,
      onGridReady2,
      defaultColDef,
      onRowSelected,
      onCellValueChanged,
      search,
      searchKeyword,
      searchTarget,
      addToCart,
      subCategoryGridContainer
    }
  }
}
</script>

<style scoped>
.content {
  background: #f3f4f6;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1)
}

button {
  transition: background-color 0.2s;
}

button:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2)
}

.arrow-right {
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-left: 10px solid black;
}

.arrow-container {
  justify-content: center;
}

.cart-button {
  position: fixed;
  bottom: 20px;
  right: 20px;
  padding: 5px 10px;
  color: #000000;
  font-size: 12px;
  border: 2.5px solid #D1D4DA;
  border-radius: 5px;
  cursor: pointer;
  z-index: 99;
}

.cart-popup {
  position: fixed;
  bottom: 70px;
  right: 20px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 20px;
  max-height: 500px;
  width: 500px;
  overflow-y: auto;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  z-index: 999;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
}

.ag-theme-alpine {
  height: 300px;
}

.example-wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

#myGrid {
  flex: 1 1 0px;
  width: 100%;
}

.example-header {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 13px;
  margin-bottom: 5px;
}
</style>
