import { createStore } from 'vuex'
import createPersistedState from 'vuex-persistedstate'

export default createStore({
  state: {
    usrInfo: {
      usrId: null,
      usrName: null,
      usrCom: null,
      imgLogo: null
    }
  },
  getters: {
  },
  mutations: {
    setUserInfo (state, userInfo) {
      state.usrInfo.usrId = userInfo.usrId
      state.usrInfo.usrName = userInfo.usrName
      state.usrInfo.usrCom = userInfo.usrCom
      state.usrInfo.imgLogo = userInfo.imgLogo
    }
  },
  actions: {
  },
  modules: {
  },
  plugins: [
    createPersistedState()
  ]
})
