<template>
  <div class="content" style="overflow-y: scroll;">
    <h1 class="text-2xl font-bold mb-4"><i class="fa-solid fa-chart-pie"></i> 통계</h1>
    <div class="grid grid-cols-1 md:grid-cols-2 gap-6">
      <!-- 연간 및 월간 주문금액 차트 -->
      <div class="bg-white p-6 rounded-lg shadow border border-gray-300">
        <div class="flex justify-between items-center mb-4">
          <div class="font-bold text-2xl">주문금액</div>
          <ul class="flex space-x-2">
            <li :class="{'bg-blue-500 text-white': activeSalesTab === 'yearly'}" class="px-4 py-2 cursor-pointer rounded-lg hover:bg-blue-400" @click="setActiveSalesTab('yearly')">연간</li>
            <li :class="{'bg-blue-500 text-white': activeSalesTab === 'monthly'}" class="px-4 py-2 cursor-pointer rounded-lg hover:bg-blue-400" @click="setActiveSalesTab('monthly')">월간</li>
          </ul>
        </div>
        <!-- Chart.js 바 차트 컴포넌트 -->
        <BarChart :chartData="activeSalesTab === 'yearly' ? yearlySalesData : monthlySalesData" :options="chartOptions" style="height: 300px;" />
        <!-- ag-Grid 컴포넌트 -->
        <ag-grid-vue
          class="ag-theme-alpine mt-6"
          :columnDefs="activeSalesTab === 'yearly' ? yearlyColumnDefs : monthlyColumnDefs"
          :rowData="activeSalesTab === 'yearly' ? yearlySalesRows : monthlySalesRows"
          :defaultColDef="{ flex: 1 }"
          style="width: 100%; height: 250px;"
        ></ag-grid-vue>
      </div>

      <!-- 주소지별 주문금액 차트 -->
      <div class="bg-white p-6 rounded-lg shadow border border-gray-300">
        <div class="flex justify-between items-center mb-4">
          <div class="font-bold text-2xl">주소지별 주문금액</div>
          <ul class="flex space-x-2">
            <li :class="{'bg-blue-500 text-white': activeAliasTab === 'yearly'}" class="px-4 py-2 cursor-pointer rounded-lg hover:bg-blue-400" @click="setActiveAliasTab('yearly')">연간</li>
            <li :class="{'bg-blue-500 text-white': activeAliasTab === 'monthly'}" class="px-4 py-2 cursor-pointer rounded-lg hover:bg-blue-400" @click="setActiveAliasTab('monthly')">월간</li>
          </ul>
        </div>
        <!-- Chart.js 바 차트 컴포넌트 -->
        <BarChart :chartData="activeAliasTab === 'yearly' ? yearlyAliasData : monthlyAliasData" :options="chartOptions" style="height: 300px;" />
        <!-- ag-Grid 컴포넌트 -->
        <ag-grid-vue
          class="ag-theme-alpine mt-6"
          :columnDefs="activeAliasTab === 'yearly' ? yearlyAliasColumnDefs : monthlyAliasColumnDefs"
          :rowData="activeAliasTab === 'yearly' ? yearlyAliasRows : monthlyAliasRows"
          :defaultColDef="{ flex: 1 }"
          style="width: 100%; height: 250px;"
        ></ag-grid-vue>
      </div>
    </div>

    <div class="mt-6">
      <!-- 가장 많이 팔린 물품 차트 -->
      <div class="bg-white p-6 rounded-lg shadow border border-gray-300">
        <div class="flex justify-between items-center mb-4">
          <div class="font-bold text-2xl">가장 많이 팔린 물품</div>
          <ul class="flex space-x-2">
            <li :class="{'bg-blue-500 text-white': activeProductsTab === 'yearly'}" class="px-4 py-2 cursor-pointer rounded-lg hover:bg-blue-400" @click="setActiveProductsTab('yearly')">연간</li>
            <li :class="{'bg-blue-500 text-white': activeProductsTab === 'monthly'}" class="px-4 py-2 cursor-pointer rounded-lg hover:bg-blue-400" @click="setActiveProductsTab('monthly')">월간</li>
          </ul>
        </div>
        <!-- Chart.js 바 차트 컴포넌트 -->
        <BarChart :chartData="activeProductsTab === 'yearly' ? yearlyProductsData : monthlyProductsData" :options="chartOptions" style="height: 460px;" />
        <!-- ag-Grid 컴포넌트 -->
        <ag-grid-vue
          class="ag-theme-alpine mt-6"
          :columnDefs="activeProductsTab === 'yearly' ? yearlyProductsColumnDefs : monthlyProductsColumnDefs"
          :rowData="activeProductsTab === 'yearly' ? yearlyProductsRows : monthlyProductsRows"
          :defaultColDef="{ flex: 1 }"
          style="width: 100%; height: 250px;"
        ></ag-grid-vue>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import axios from 'axios'
import store from '@/store'
import BarChart from '@/components/BarChart.vue'
import { AgGridVue } from 'ag-grid-vue3'
import {
  Chart as ChartJS,
  LineElement,
  PointElement,
  LineController,
  CategoryScale,
  LinearScale,
  Title,
  Tooltip,
  Legend
} from 'chart.js'

// Chart.js 요소 등록
ChartJS.register(
  LineElement,
  PointElement,
  LineController,
  CategoryScale,
  LinearScale,
  Title,
  Tooltip,
  Legend
)

export default {
  name: 'ChartTabs',
  components: {
    AgGridVue,
    BarChart
  },
  setup () {
    // 활성 탭 상태 정의
    const activeSalesTab = ref('yearly')
    const setActiveSalesTab = tab => { activeSalesTab.value = tab }
    const activeProductsTab = ref('yearly')
    const setActiveProductsTab = tab => { activeProductsTab.value = tab }
    const activeAliasTab = ref('yearly')
    const setActiveAliasTab = tab => { activeAliasTab.value = tab }

    // 사용자 회사명 가져오기
    const usrCom = store.state.usrInfo.usrCom

    // 차트와 그리드 데이터를 저장할 상태 정의
    const yearlySalesData = ref({ labels: [], datasets: [] })
    const monthlySalesData = ref({ labels: [], datasets: [] })
    const yearlyProductsData = ref({ labels: [], datasets: [] })
    const monthlyProductsData = ref({ labels: [], datasets: [] })
    const yearlyAliasData = ref({ labels: [], datasets: [] })
    const monthlyAliasData = ref({ labels: [], datasets: [] })

    const yearlySalesRows = ref([])
    const monthlySalesRows = ref([])
    const yearlyProductsRows = ref([])
    const monthlyProductsRows = ref([])
    const yearlyAliasRows = ref([])
    const monthlyAliasRows = ref([])

    // 차트 옵션 정의
    const chartOptions = {
      responsive: true,
      plugins: {
        legend: {
          position: 'top'
        }
      }
    }

    // 숫자 포맷팅 함수
    const formatNumber = num => {
      return `${num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')} 원`
    }

    const formatNumber2 = num => {
      return `${num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')} 개`
    }

    // 그리드 컬럼 정의
    const yearlyColumnDefs = ref([
      { field: 'year', headerName: '년도', cellStyle: { 'text-align': 'center' } },
      { field: 'total_sales', headerName: '금액', valueFormatter: params => formatNumber(params.value), cellStyle: { 'text-align': 'right' } }
    ])
    const monthlyColumnDefs = ref([
      { field: 'month', headerName: '개월', cellStyle: { 'text-align': 'center' } },
      { field: 'total_sales', headerName: '금액', valueFormatter: params => formatNumber(params.value), cellStyle: { 'text-align': 'right' } }
    ])
    const yearlyProductsColumnDefs = ref([
      { field: 'year', headerName: '년도', cellStyle: { 'text-align': 'center' } },
      { field: 'ods_nam', headerName: '품목명' },
      { field: 'total_quantity', headerName: '개수', valueFormatter: params => formatNumber2(params.value), cellStyle: { 'text-align': 'right' } }
    ])
    const monthlyProductsColumnDefs = ref([
      { field: 'month', headerName: '개월', cellStyle: { 'text-align': 'center' } },
      { field: 'ods_nam', headerName: '품목명' },
      { field: 'total_quantity', headerName: '개수', valueFormatter: params => formatNumber2(params.value), cellStyle: { 'text-align': 'right' } }
    ])
    const yearlyAliasColumnDefs = ref([
      { field: 'year', headerName: '년도', cellStyle: { 'text-align': 'center' } },
      { field: 'odr_als', headerName: '별칭' },
      { field: 'total_sales', headerName: '금액', valueFormatter: params => formatNumber(params.value), cellStyle: { 'text-align': 'right' } }
    ])
    const monthlyAliasColumnDefs = ref([
      { field: 'month', headerName: '개월', cellStyle: { 'text-align': 'center' } },
      { field: 'odr_als', headerName: '별칭' },
      { field: 'total_sales', headerName: '금액', valueFormatter: params => formatNumber(params.value) }
    ])

    // 연간 매출 데이터 가져오기
    const fetchYearlySales = async (usrCom) => {
      try {
        const response = await axios.get('/api/stats1_1', { params: { usrCom } })
        const data = response.data
        console.log(data)
        yearlySalesData.value = {
          labels: data.map(item => item.year),
          datasets: [{
            type: 'line',
            label: '총 판매액',
            backgroundColor: '#4caf50',
            borderWidth: 3,
            borderColor: '#4caf50',
            data: data.map(item => item.total_sales)
          }]
        }
        yearlySalesRows.value = data
      } catch (error) {
        console.error('Error fetching yearly sales data:', error)
      }
    }

    // 월간 매출 데이터 가져오기
    const fetchMonthlySales = async (usrCom) => {
      try {
        const response = await axios.get('/api/stats1_2', { params: { usrCom } })
        const data = response.data
        monthlySalesData.value = {
          labels: data.map(item => item.month),
          datasets: [{
            type: 'line',
            label: '총 판매액',
            backgroundColor: '#4caf50',
            borderWidth: 3,
            borderColor: '#4caf50',
            data: data.map(item => item.total_sales)
          }]
        }
        monthlySalesRows.value = data
      } catch (error) {
        console.error('Error fetching monthly sales data:', error)
      }
    }

    // 연간 인기 상품 데이터 가져오기
    const fetchYearlyTopProducts = async (usrCom) => {
      try {
        const response = await axios.get('/api/stats2_1', { params: { usrCom } })
        const data = response.data
        yearlyProductsData.value = {
          labels: data.map(item => item.ods_nam),
          datasets: [{
            label: '총 수량',
            backgroundColor: ['#14ff50', '#2196f3', '#ffeb3b', '#ff5722', '#9c27b0'],
            data: data.map(item => item.total_quantity)
          }]
        }
        yearlyProductsRows.value = data
      } catch (error) {
        console.error('Error fetching yearly top products data:', error)
      }
    }

    // 월간 인기 상품 데이터 가져오기
    const fetchMonthlyTopProducts = async (usrCom) => {
      try {
        const response = await axios.get('/api/stats2_2', { params: { usrCom } })
        const data = response.data
        monthlyProductsData.value = {
          labels: data.map(item => item.ods_nam),
          datasets: [{
            label: '총 수량',
            backgroundColor: ['#14ff50', '#2196f3', '#ffeb3b', '#ff5722', '#9c27b0'],
            data: data.map(item => item.total_quantity)
          }]
        }
        monthlyProductsRows.value = data
      } catch (error) {
        console.error('Error fetching monthly top products data:', error)
      }
    }

    // 연간 주소지별 매출 데이터 가져오기
    const fetchYearlyAliasSales = async (usrCom) => {
      try {
        const response = await axios.get('/api/stats3_1', { params: { usrCom } })
        const data = response.data
        yearlyAliasData.value = {
          labels: data.map(item => item.odr_als),
          datasets: [{
            label: '총 판매액',
            backgroundColor: ['#4caf50', '#2196f3', '#ffeb3b', '#ff5722', '#9c27b0'],
            data: data.map(item => item.total_sales)
          }]
        }
        yearlyAliasRows.value = data
      } catch (error) {
        console.error('Error fetching yearly alias sales data:', error)
      }
    }

    // 월간 주소지별 매출 데이터 가져오기
    const fetchMonthlyAliasSales = async (usrCom) => {
      try {
        const response = await axios.get('/api/stats3_2', { params: { usrCom } })
        const data = response.data
        monthlyAliasData.value = {
          labels: data.map(item => item.odr_als),
          datasets: [{
            label: '총 판매액',
            backgroundColor: ['#4caf50', '#2196f3', '#ffeb3b', '#ff5722', '#9c27b0'],
            data: data.map(item => item.total_sales)
          }]
        }
        monthlyAliasRows.value = data
      } catch (error) {
        console.error('Error fetching monthly alias sales data:', error)
      }
    }

    // 컴포넌트가 마운트될 때 데이터 가져오기
    onMounted(() => {
      fetchYearlySales(usrCom)
      fetchMonthlySales(usrCom)
      fetchYearlyTopProducts(usrCom)
      fetchMonthlyTopProducts(usrCom)
      fetchYearlyAliasSales(usrCom)
      fetchMonthlyAliasSales(usrCom)
    })

    return {
      activeSalesTab,
      setActiveSalesTab,
      activeProductsTab,
      setActiveProductsTab,
      activeAliasTab,
      setActiveAliasTab,
      yearlySalesData,
      monthlySalesData,
      yearlyProductsData,
      monthlyProductsData,
      yearlyAliasData,
      monthlyAliasData,
      chartOptions,
      yearlyColumnDefs,
      monthlyColumnDefs,
      yearlyProductsColumnDefs,
      monthlyProductsColumnDefs,
      yearlyAliasColumnDefs,
      monthlyAliasColumnDefs,
      yearlySalesRows,
      monthlySalesRows,
      yearlyProductsRows,
      monthlyProductsRows,
      yearlyAliasRows,
      monthlyAliasRows
    }
  }
}
</script>

<style scoped>
</style>
