<template>
  <div class="content p-6 bg-gray-100" style="overflow-y: scroll;">
    <h1 class="text-2xl font-bold mb-6"><i class="fa-solid fa-house"></i> HOME</h1>
    <div class="grid grid-cols-1 md:grid-cols-2 gap-6">
      <!-- 문의게시판 섹션 -->
      <div class="bg-white p-4 rounded-lg shadow-lg">
        <div class="flex justify-between items-center mb-4">
          <h2 class="text-2xl font-semibold">문의게시판</h2>
          <router-link to="/contract/Not" class="text-blue-500 hover:text-blue-700">+더보기</router-link>
        </div>
        <ag-grid-vue
          class="ag-theme-alpine"
          :columnDefs="noticeColumnDefs"
          :rowData="notices"
          :defaultColDef="{ flex: 1 }"
          style="width: 100%; height: 300px;"
          @cell-clicked="onCellClicked"
        ></ag-grid-vue>
        <!-- 선택된 공지사항의 상세 내용을 보여주는 섹션 -->
        <div v-if="selectedNotice" class="mt-4 p-4 bg-white rounded-lg shadow-lg">
          <div class="flex justify-between items-center mb-2">
            <h3 class="text-xl font-semibold">제목: {{ selectedNotice.tb_tit }}</h3>
            <button @click="selectedNotice = null" class="text-red-500 hover:text-red-700 font-bold">X</button>
          </div>
          <p>{{ selectedNotice.tb_txt }}</p>
        </div>
      </div>

      <!-- 최근 자주 구매한 물품 섹션 -->
      <div class="bg-white p-4 rounded-lg shadow-lg">
        <div class="flex justify-between items-center mb-4">
          <h2 class="text-2xl font-semibold">최근 자주 구매한 물품 (TOP 10)</h2>
          <router-link to="/contract/created" class="text-blue-500 hover:text-blue-700">+더보기</router-link>
        </div>
        <ag-grid-vue
          class="ag-theme-alpine"
          :columnDefs="frequentItemsColumnDefs"
          :rowData="frequentItems"
          :defaultColDef="{ flex: 1 }"
          style="width: 100%; height: 300px;"
        ></ag-grid-vue>
      </div>
    </div>

    <!-- 최근 주문 내역 섹션 -->
    <div class="mt-6 bg-white p-4 rounded-lg shadow-lg flex">
      <div class="w-4/5">
        <div class="flex justify-between items-center mb-4">
          <h2 class="text-2xl font-semibold">최근 주문 내역</h2>
          <router-link to="/contract/created" class="text-blue-500 hover:text-blue-700">+더보기</router-link>
        </div>
        <ag-grid-vue
          class="ag-theme-alpine"
          :columnDefs="orderHistoryColumnDefs"
          :rowData="orderHistory"
          :defaultColDef="{ flex: 1 }"
          style="width: 100%; height: 300px;"
        ></ag-grid-vue>
      </div>
      <div class="w-1/5 p-4">
        <div class="flex flex-col space-y-4">
          <div class="bg-white shadow p-4 rounded-lg flex items-center">
            <i class="fas fa-check-circle text-green-500 text-2xl mr-4"></i>
            <span>주문완료: {{ orderStatusCounts.ordered }} 건</span>
          </div>
          <div class="bg-white shadow p-4 rounded-lg flex items-center">
            <i class="fas fa-credit-card text-blue-500 text-2xl mr-4"></i>
            <span>입금완료: {{ orderStatusCounts.paid }} 건</span>
          </div>
          <div class="bg-white shadow p-4 rounded-lg flex items-center">
            <i class="fas fa-truck text-yellow-500 text-2xl mr-4"></i>
            <span>배송완료: {{ orderStatusCounts.shipped }} 건</span>
          </div>
          <div class="bg-white shadow p-4 rounded-lg flex items-center">
            <i class="fas fa-file-invoice text-purple-500 text-2xl mr-4"></i>
            <span>계산서발행완료: {{ orderStatusCounts.invoiced }} 건</span>
          </div>
          <div class="bg-white shadow p-4 rounded-lg flex items-center">
            <i class="fas fa-times-circle text-red-500 text-2xl mr-4"></i>
            <span>주문취소: {{ orderStatusCounts.cancelled }} 건</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { ref, onMounted } from 'vue'
import { AgGridVue } from 'ag-grid-vue3'
import axios from 'axios'
import store from '@/store'

export default {
  name: 'HomeTabs',
  components: {
    AgGridVue
  },
  setup () {
    // 공지사항 및 자주 구매한 물품 리스트 상태
    const notices = ref([])
    const frequentItems = ref([])
    const orderHistory = ref([])
    const selectedNotice = ref(null)
    // 사용자 회사명
    const usrCom = store.state.usrInfo.usrCom
    const usrId = store.state.usrInfo.usrId

    // 날짜 형식을 "YYYY/MM/DD"로 변환하는 함수
    const formatDate = (dateString) => {
      return dateString.replace(/(\d{4})(\d{2})(\d{2})/, '$1/$2/$3')
    }

    // 숫자를 천 단위 콤마로 포맷팅하는 함수
    const formatNumber = (num) => {
      return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
    }

    // 공지사항 그리드 컬럼 정의
    const noticeColumnDefs = [
      { field: 'tb_num', headerName: '번호', minWidth: 70, maxWidth: 70, cellStyle: { 'text-align': 'center' } },
      {
        field: 'tb_div',
        headerName: '구분',
        cellStyle: { 'text-align': 'center' },
        minWidth: 70,
        maxWidth: 110,
        cellRenderer: params => {
          const div = params.value === 1 ? 'bg-red-200 text-red-800' : 'bg-blue-200 text-blue-800'
          const text = params.value === 1 ? '공지사항' : '일반문의'
          return `<span class="px-2 py-1 rounded ${div}">${text}</span>`
        }
      },
      {
        field: 'tb_tit',
        headerName: '제목',
        cellRenderer: params => {
          return `<span class="text-blue-500 cursor-pointer">${params.value}</span> ${params.data.tb_div !== 1 ? `<span class="text-gray-500">[${params.data.comment_count}]</span>` : ''}`
        }
      },
      { field: 'tb_dat', headerName: '작성일', cellStyle: { 'text-align': 'center' } },
      { field: 'tb_nam', headerName: '작성자', minWidth: 70, maxWidth: 100, cellStyle: { 'text-align': 'center' } }
    ]

    // 자주 구매한 물품 그리드 컬럼 정의
    const frequentItemsColumnDefs = [
      {
        field: 'img_itm',
        headerName: '이미지',
        cellRenderer: params => `<img src="/img/item/${params.value}" style="width: 40px; height: 40px;" />`,
        minWidth: 90,
        maxWidth: 90,
        cellStyle: { 'text-align': 'center' }
      },
      { field: 'ods_nam', headerName: '품목명' },
      {
        field: 'purchase_count',
        headerName: '구매 횟수',
        minWidth: 70,
        maxWidth: 100,
        cellStyle: { 'text-align': 'center' },
        valueFormatter: params => `${formatNumber(params.value)} 개`
      }
    ]

    // 최근 주문 내역 그리드 컬럼 정의
    const orderHistoryColumnDefs = [
      {
        field: 'odr_dat',
        headerName: '주문일자',
        cellStyle: { 'text-align': 'center' },
        minWidth: 100,
        maxWidth: 150,
        valueFormatter: params => formatDate(params.value)
      },
      { field: 'odr_id', headerName: '주문번호', minWidth: 150, maxWidth: 200, cellStyle: { 'text-align': 'center' } },
      { field: 'ods_nam', headerName: '품목명' },
      {
        field: 'ods_qty',
        headerName: '수량',
        minWidth: 100,
        maxWidth: 150,
        cellStyle: { 'text-align': 'center' },
        valueFormatter: params => `${formatNumber(params.value)} 개`
      },
      {
        field: 'payment_total',
        headerName: '총액',
        minWidth: 100,
        maxWidth: 150,
        cellStyle: { 'text-align': 'center' },
        valueFormatter: params => `${formatNumber(params.value)} 원`
      },
      {
        field: 'odr_drd',
        headerName: '배송요청일',
        minWidth: 100,
        maxWidth: 150,
        cellStyle: { 'text-align': 'center' },
        valueFormatter: params => formatDate(params.value)
      },
      {
        field: 'odr_sts',
        headerName: '처리상태',
        minWidth: 100,
        maxWidth: 150,
        cellStyle: { 'text-align': 'center' },
        valueFormatter: params => {
          switch (params.value) {
            case 0: return '주문완료'
            case 1: return '주문취소'
            case 2: return '입금완료'
            case 3: return '배송완료'
            case 4: return '계산서발행완료'
            default: return params.value
          }
        }
      }
    ]

    // 주문 상태별 카운트
    const orderStatusCounts = ref({
      ordered: 0,
      paid: 0,
      shipped: 0,
      invoiced: 0,
      cancelled: 0
    })
    // 공지사항 데이터 가져오기 함수
    const fetchNotices = async () => {
      try {
        const response = await axios.get('/api/notices', { params: { usrId } })
        notices.value = response.data
      } catch (error) {
        console.error('공지를 가져오는 중에 오류가 발생했습니다:', error)
      }
    }

    // 자주 구매한 물품 데이터 가져오기 함수
    const fetchFrequentItems = async (usrCom) => {
      try {
        const response = await axios.get('/api/frequent-items', { params: { usrCom } })
        frequentItems.value = response.data
      } catch (error) {
        console.error('Error fetching frequent items:', error)
      }
    }

    // 최근 주문 내역 데이터 가져오기 함수
    const fetchOrderHistory = async () => {
      try {
        const response = await axios.get('/api/order-history', { params: { usrId } })
        orderHistory.value = response.data
        // 주문 상태별 카운트 계산
        const counts = {
          ordered: 0,
          paid: 0,
          shipped: 0,
          invoiced: 0,
          cancelled: 0
        }
        orderHistory.value.forEach(order => {
          switch (order.odr_sts) {
            case 0:
              counts.ordered++
              break
            case 1:
              counts.cancelled++
              break
            case 2:
              counts.paid++
              break
            case 3:
              counts.shipped++
              break
            case 4:
              counts.invoiced++
              break
          }
        })
        orderStatusCounts.value = counts
      } catch (error) {
        console.error('Error fetching order history:', error)
      }
    }

    // 셀 클릭 시 처리 함수
    const onCellClicked = params => {
      if (params.colDef.field === 'tb_tit') {
        selectedNotice.value = params.data
      }
    }

    // 컴포넌트가 마운트될 때 데이터 가져오기
    onMounted(() => {
      fetchNotices()
      fetchFrequentItems(usrCom)
      fetchOrderHistory()
    })

    return {
      notices,
      frequentItems,
      orderHistory,
      selectedNotice,
      noticeColumnDefs,
      frequentItemsColumnDefs,
      orderHistoryColumnDefs,
      onCellClicked,
      orderStatusCounts
    }
  }
}
</script>

<style scoped>
</style>
