<template>
  <div class="modal" v-if="props.visible">
    <div class="overlay bg-black opacity-50 fixed inset-0 z-10"></div>
    <div class="modal-card bg-white p-6 mx-auto mt-16 rounded shadow-lg z-20 max-w-lg w-full">
      <button class="close-button absolute top-3 right-3 text-xl" @click="emit('close')">X</button>
      <h2 class="text-center text-xl font-semibold mb-4">{{ currentView === 'list' ? '계좌 목록' : '계좌 정보 추가' }}</h2>

      <!-- 계좌 목록 표시 -->
      <div v-if="currentView === 'list'">
        <div class="delivery-address-list overflow-y-auto max-h-96">
          <div class="delivery-address-item border-b border-gray-300 py-2" v-for="address in addresses"
            :key="address.id">
            <div class="delivery-info flex justify-between items-center w-full">
              <div class="w-3/4">
                <div class="delivery-name font-semibold">
                  <span class="font-bold">{{ address.usr_owner }}</span>&nbsp;<span v-if="address.usr_mus === 'Y'"
                    class="inline-block border border-slate-400 rounded-full px-1 text-xs">기본 계좌</span><br>
                </div>
                <div class="delivery-details text-sm">
                  {{ address.usr_bank }}<br>
                  {{ address.usr_acc }}<br>
                  <img :src="'/img/bank/' + address.img_acc" alt="통장 이미지" v-if="address.img_acc">
                </div>
              </div>
              <div class="flex justify-between items-center">
                <button class="text-white bg-green-500 hover:bg-green-700 rounded px-4 py-1"
                  @click="editAddress(address)">수정</button>
              </div>
            </div>
          </div>
        </div>
        <button class="add-delivery-address mt-4 bg-blue-500 hover:bg-blue-700 text-white rounded px-6 py-2"
          @click="changeView('add')">+ 계좌 추가</button>
      </div>

      <!-- 새 계좌 추가 폼 -->
      <div v-else-if="currentView === 'add'">
        <form @submit.prevent="submitNewAddress" class="space-y-4">
          <input v-model="newAddress.usr_bank" placeholder="은행명" required class="w-full px-4 py-2 border rounded">
          <input v-model="newAddress.usr_acc" placeholder="계좌번호" required class="w-full px-4 py-2 border rounded">
          <input v-model="newAddress.usr_owner" placeholder="예금주" required class="w-full px-4 py-2 border rounded">
          <input type="file" @change="handleFileUpload" class="w-96 px-4 py-2 border rounded">
          <label class="block">
            <input type="checkbox" v-model="newAddress.usr_mus" class="mr-2"> 기본 계좌로 설정
          </label>
          <button type="submit" class="w-full bg-blue-500 hover:bg-blue-700 text-white rounded py-2">계좌 추가</button>
        </form>
        <button @click="changeView('list')"
          class="mt-4 w-full bg-gray-500 hover:bg-gray-700 text-white rounded py-2">목록으로 돌아가기</button>
      </div>

      <!-- 계좌 수정 폼 -->
      <div v-if="currentView === 'update'">
        <form @submit.prevent="submitEditAddress" class="space-y-4">
          <input v-model="editingAddress.usr_bank" placeholder="은행명" required class="w-full px-4 py-2 border rounded">
          <input v-model="editingAddress.usr_acc" placeholder="계좌번호" required class="w-full px-4 py-2 border rounded">
          <input v-model="editingAddress.usr_owner" placeholder="예금주" required class="w-full px-4 py-2 border rounded">
          <input type="file" @change="handleImageUpload" class="w-full px-4 py-2 border rounded">
          <img :src="imagePreview || '/img/bank/' + editingAddress.img_acc" alt="통장 이미지" v-if="editingAddress.img_acc || imagePreview" class="mt-2">
          <label class="block">
            <input type="checkbox" v-model="editingAddress.usr_mus" class="mr-2"> 기본 계좌로 설정
          </label>
          <button type="submit" class="w-full bg-sky-500 hover:bg-sky-700 text-white rounded py-2">수정 완료</button>
        </form>
        <button @click="handleDelete(editingAddress.usr_num)" class="w-full bg-slate-100 hover:bg-slate-400 text-red rounded py-2">삭제</button>
      </div>
    </div>
  </div>
</template>

<script setup>
import { defineProps, defineEmits, ref, reactive } from 'vue'
import axios from 'axios'
import store from '@/store'

const props = defineProps({
  visible: Boolean,
  addresses: Array
})

const emit = defineEmits(['close', 'updated'])
const usrId = store.state.usrInfo.usrId
const currentView = ref('list')
const editingAddress = ref(null)
const imagePreview = ref(null)
const newAddress = reactive({
  usrId,
  usr_acc: '',
  usr_bank: '',
  usr_owner: '',
  img_acc: '',
  usr_mus: false
})

const changeView = view => { currentView.value = view }

const editAddress = address => {
  editingAddress.value = { ...address }
  editingAddress.value.usr_mus = address.usr_mus === 'Y'
  imagePreview.value = null
  currentView.value = 'update'
}

// 파일 업로드 핸들러
const handleFileUpload = event => {
  const file = event.target.files[0]
  if (file) {
    newAddress.img_acc = file
  }
}

// 이미지 업로드 핸들러
const handleImageUpload = event => {
  const file = event.target.files[0]
  if (file) {
    editingAddress.value.img_acc = file
    const reader = new FileReader()
    reader.onload = e => {
      imagePreview.value = e.target.result
    }
    reader.readAsDataURL(file)
  }
}

// 계좌 추가
const submitNewAddress = async () => {
  try {
    const formData = new FormData()
    formData.append('usr_id', usrId)
    formData.append('usr_acc', newAddress.usr_acc)
    formData.append('usr_bank', newAddress.usr_bank)
    formData.append('usr_owner', newAddress.usr_owner)
    formData.append('usr_mus', newAddress.usr_mus ? 'Y' : 'N')
    if (newAddress.img_acc) {
      formData.append('img_acc', newAddress.img_acc)
    }

    await axios.post('/api/acc/add', formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
    emit('updated')
    Object.keys(newAddress).forEach(key => { newAddress[key] = '' })
    changeView('list')
  } catch (error) {
    console.error('계좌 추가 중 오류 발생:', error)
    alert('계좌 추가 중 오류가 발생했습니다.')
  }
}

// 계좌 수정
const submitEditAddress = async () => {
  const formData = new FormData()
  formData.append('usrId', usrId)
  formData.append('usr_acc', editingAddress.value.usr_acc)
  formData.append('usr_bank', editingAddress.value.usr_bank)
  formData.append('usr_owner', editingAddress.value.usr_owner)
  formData.append('usr_mus', editingAddress.value.usr_mus ? 'Y' : 'N')
  if (editingAddress.value.img_acc instanceof File) {
    formData.append('img_acc', editingAddress.value.img_acc)
  }

  try {
    await axios.put(`/api/acc/update/${editingAddress.value.usr_num}`, formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
    alert('계좌 정보가 수정되었습니다.')
    emit('updated')
    editingAddress.value = null
    currentView.value = 'list'
  } catch (error) {
    console.error('계좌 수정 중 오류 발생:', error)
    alert('계좌 수정 중 오류가 발생했습니다.')
  }
}

// 계좌 삭제
const handleDelete = async (usrNum) => {
  if (!confirm('이 계좌를 삭제하시겠습니까?')) return
  try {
    await axios.delete(`/api/acc/delete/${usrNum}`)
    alert('계좌 정보가 삭제되었습니다.')
    emit('updated')
    currentView.value = 'list'
  } catch (error) {
    console.error('계좌 삭제 중 오류 발생:', error)
    alert('계좌 삭제 중 오류가 발생했습니다.')
  }
}
</script>

<style scoped>
.modal,
.overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10;
}

.overlay {
  opacity: 0.5;
  background-color: black;
}

.modal-card {
  position: relative;
  max-width: 600px;
  margin: auto;
  margin-top: 100px;
  padding: 20px;
  background-color: #f6f6f6;
  min-height: 700px;
  z-index: 11;
  border: 1px solid black;
  opacity: 1;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 1.5em;
  cursor: pointer;
}

.delivery-address-list {
  max-height: 600px;
  overflow-y: auto;
}

.delivery-address-item {
  border-bottom: 1px solid #ccc;
  padding: 10px;
  display: flex;
  justify-content: space-between;
}

.delivery-select button {
  padding: 5px 10px;
  margin: 0 5px;
  cursor: pointer;
}

.add-delivery-address button {
  padding: 5px 15px;
  cursor: pointer;
}
</style>
