import { createRouter, createWebHistory } from 'vue-router'
import ScmLoginView from '@/views/scm/ScmLoginView.vue'
import ContractHomeView from '@/views/scm/ContractHomeView.vue'
import OrderListView from '@/views/scm/SearchContractList/OrderListView.vue'
import Ordercart from '@/views/scm/SearchContractList/Ordercart.vue'
import Orderpayment from '@/views/scm/SearchContractList/Orderpayment.vue'
import Created from '@/views/scm/SearchContractList/Created.vue'
import Info from '@/views/scm/SearchContractList/User_Info.vue'
import Company from '@/views/scm/SearchContractList/User_Company.vue'
import Dvy from '@/views/scm/SearchContractList/User_Dvy.vue'
import Acc from '@/views/scm/SearchContractList/User_Acc.vue'
import Not from '@/views/scm/SearchContractList/Notifi.vue'
import Total from '@/views/scm/SearchContractList/Total.vue'

const routes = [
  {
    path: '/',
    name: 'ScmLoginView',
    component: ScmLoginView
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path: '/contract',
    name: 'contract',
    component: () => import(/* webpackChunkName: "contract" */ '../views/scm/Contract'),
    // meta: { authRequired: true },
    children: [
      {
        path: 'contractHome',
        name: 'ContractHomeView',
        component: ContractHomeView
      },
      {
        path: 'orderListView',
        name: 'OrderListView',
        component: OrderListView
      },
      {
        path: 'ordercart',
        name: 'Ordercart',
        component: Ordercart
      },
      {
        path: 'orderpayment',
        name: 'Orderpayment',
        component: Orderpayment
      },
      {
        path: 'created',
        name: 'Created',
        component: Created
      },
      {
        path: 'company',
        name: 'Company',
        component: Company
      },
      {
        path: 'info',
        name: 'Info',
        component: Info
      },
      {
        path: 'dvy',
        name: 'Dvy',
        component: Dvy
      },
      {
        path: 'acc',
        name: 'Acc',
        component: Acc
      },
      {
        path: 'not',
        name: 'Not',
        component: Not
      },
      {
        path: 'total',
        name: 'Total',
        component: Total
      }
    ]
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
