<template>
  <div class="content">
    <div class="mx-4 my-5 h-full">
      <div class="flex justify-between items-center mb-4">
        <h1 class='text-2xl font-bold'><i class="fa-solid fa-truck"></i> 배송지정보</h1>
        <button class="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded" @click="toggleModal">배송지 추가</button>
      </div>

      <!-- Ag-Grid 테이블 -->
      <div class="flex mx-1 h-full">
        <div class="w-full">
          <ag-grid-vue class='ag-theme-alpine' style='width: 100%; height: 600px;' :columnDefs='colDefs' :rowData='rowData' rowSelection='multiple' suppressRowClickSelection='true' alwaysShowVerticalScroll='true' :defaultColDef='defaultColDef'></ag-grid-vue>
        </div>
      </div>

      <!-- 모달 -->
      <DvyModal v-if="showDvyModal" :visible="showDvyModal" :addresses="rowData" @close="toggleModal" @updated="fetchContacts"/>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import 'ag-grid-community/styles/ag-grid.css'
import 'ag-grid-community/styles/ag-theme-alpine.css'
import { AgGridVue } from 'ag-grid-vue3'
import axios from 'axios'
import store from '@/store/index.js'
import DvyModal from '@/components/modal/DvyModal.vue'

export default {
  name: 'ContactInfo',
  components: {
    AgGridVue,
    DvyModal
  },
  setup () {
    const colDefs = ref([
      { headerName: '사업자등록번호', field: 'dv_id', minWidth: 120, maxWidth: 350, sortable: true, hide: true },
      { headerName: '배송지명', field: 'dv_dnm', minWidth: 120, maxWidth: 220, sortable: true, cellStyle: { 'text-align': 'center' } },
      { headerName: '별칭', field: 'dv_als', minWidth: 120, maxWidth: 220, sortable: true, cellStyle: { 'text-align': 'center' } },
      { headerName: '주소', field: 'dv_adr', minWidth: 120, maxWidth: 350, sortable: true, cellStyle: { 'text-align': 'center' } },
      { headerName: '상세주소', field: 'dv_dti', minWidth: 120, maxWidth: 300, sortable: true, cellStyle: { 'text-align': 'center' } },
      { headerName: '연락처', field: 'dv_phn', minWidth: 150, maxWidth: 220, sortable: true, cellStyle: { 'text-align': 'center' } },
      { headerName: '받는사람', field: 'dv_nam', minWidth: 150, maxWidth: 220, sortable: true, cellStyle: { 'text-align': 'center' } }
    ])

    const usrId = ref(store.state.usrInfo.usrId)
    const rowData = ref([])
    const showDvyModal = ref(false)

    const defaultColDef = ref({
      flex: 1,
      minWidth: 100,
      filter: true,
      resizable: true,
      sortable: true
    })

    const fetchContacts = async () => {
      try {
        const response = await axios.get(`/api/delivery-addresses/${usrId.value}`)
        rowData.value = response.data
      } catch (error) {
        console.error('배송지 정보를 불러오는데 실패했습니다.', error)
      }
    }

    const toggleModal = () => {
      showDvyModal.value = !showDvyModal.value
    }

    const selectAddress = address => {
      console.log('선택된 주소:', address)
      toggleModal() // 모달 닫기
    }

    onMounted(fetchContacts)

    return {
      colDefs,
      rowData,
      defaultColDef,
      fetchContacts,
      toggleModal,
      showDvyModal,
      selectAddress
    }
  }
}
</script>

<style>
.content {
  background: #f3f4f6;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1)
}

button {
  transition: background-color 0.2s;
}

button:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2)
}
</style>
