<template>
  <div class='container mx-auto mt-24 px-6 py-8 rounded-lg bg-white shadow-xl' style="max-width: 600px;">
    <div v-if="!passwordConfirmed" class='flex items-center justify-center mb-8'>
      <h3 class='text-3xl font-bold'>비밀번호 확인</h3>
    </div>
    <form v-if="!passwordConfirmed" @submit.prevent="verifyPassword" class="space-y-6 p-5">
      <div>
        <label for="usr_pwd" class="block font-medium text-gray-700">비밀번호:</label>
        <input type="password" id="usr_pwd" v-model="password" required
              class="mt-1 block w-full px-4 py-1 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500">
      </div>
      <div class="flex justify-center mt-6">
        <button type="submit" class="inline-flex justify-center py-1 px-4 border border-transparent shadow-lg text-lg font-medium rounded-md text-white bg-green-600 hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500">
          확인
        </button>
      </div>
    </form>
    <div v-if="passwordConfirmed">
      <div class='flex items-center justify-center mb-8'>
        <!-- SVG Icon Inline -->
        <svg xmlns="http://www.w3.org/2000/svg" id="Layer_1" data-name="Layer 1" viewBox="0 0 24 24" width="24" height="24" class="mr-2">
          <path d="M24,18.5v-6.176c0-3.713-1.618-7.2-4.438-9.567C16.854,.483,13.427-.43,9.908,.188,4.614,1.119,.562,5.518,.056,10.885c-.313,3.311,.733,6.473,2.946,8.904,2.435,2.676,6.092,4.211,10.032,4.211h5.466c3.033,0,5.5-2.468,5.5-5.5Zm-6.367-13.446c2.14,1.797,3.367,4.446,3.367,7.271v6.176c0,1.379-1.122,2.5-2.5,2.5h-5.466c-3.097,0-5.945-1.177-7.813-3.229-1.637-1.8-2.411-4.145-2.179-6.604,.375-3.962,3.48-7.336,7.385-8.022,.539-.095,1.074-.142,1.603-.142,2.052,0,3.999,.705,5.603,2.052Z"/>
          <path d="M13.5,7.5v4c0,.828-.672,1.5-1.5,1.5s-1.5-.672-1.5-1.5V7.5c0-.828,.672-1.5,1.5-1.5s1.5,.672,1.5,1.5Z"/>
          <path d="M12,18c-.828,0-1.5-.672-1.5-1.5s.672-1.5,1.5-1.5,1.5,.672,1.5,1.5-.672,1.5-1.5,1.5"/>
        </svg>
        <h3 class='text-3xl font-bold'>업체 정보</h3>
      </div>
      <form @submit.prevent="updateCompanyInfo" class="space-y-6 p-5">
        <div class="grid grid-cols-1 md:grid-cols-2 gap-6">
          <!-- 사업자등록번호 -->
          <div>
            <label for="usr_id" class="block font-medium text-gray-700">사업자등록번호:</label>
            <input type="text" id="usr_id" v-model="companyInfo.usrId" disabled
                  class="bg-gray-100 mt-1 block w-full px-4 py-1 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500">
          </div>

          <!-- 업체명 -->
          <div>
            <label for="usr_com" class="block font-medium text-gray-700">업체명:</label>
            <input type="text" id="usr_com" v-model="companyInfo.usr_com" required
                  class="mt-1 block w-full px-4 py-1 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500">
          </div>

          <!-- 대표자 성명 -->
          <div>
            <label for="usr_name" class="block font-medium text-gray-700">대표자 성명:</label>
            <input type="text" id="usr_name" v-model="companyInfo.usr_name" required
                  class="mt-1 block w-full px-4 py-1 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500">
          </div>

          <!-- 법인여부 -->
          <div>
            <label for="usr_corp" class="block font-medium text-gray-700">법인여부:</label>
            <select id="usr_corp" v-model="companyInfo.usr_corp" required
                  class="mt-1 block w-full px-4 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500">
              <option value="">선택하세요</option>
              <option value="Y">Y</option>
              <option value="N">N</option>
            </select>
          </div>

          <!-- 업체 주소 -->
          <div class="col-span-2">
            <label for="usr_addr" class="block font-medium text-gray-700">업체 주소:</label>
            <div class="flex">
              <input type="text" id="usr_addr" v-model="companyInfo.usr_addr" required
                    class="flex-grow mt-1 px-4 py-1 border border-gray-300 rounded-l-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500">
              <button type="button" @click="openAddressSearch" class="mt-1 bg-blue-500 hover:bg-blue-700 text-white font-bold py-1 px-4 rounded-r-md">
                검색
              </button>
            </div>
          </div>

          <!-- 상세 주소 -->
          <div class="col-span-2">
            <label for="usr_dti" class="block font-medium text-gray-700">상세주소:</label>
            <input type="text" id="usr_dti" v-model="companyInfo.usr_dti" required
                  class="mt-1 block w-full px-4 py-1 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500">
          </div>

          <!-- 대표자 연락처 -->
          <div>
            <label for="usr_type" class="block font-medium text-gray-700">대표자 연락처:</label>
            <input type="text" id="usr_type" v-model="companyInfo.usr_type" required
                  class="mt-1 block w-full px-4 py-1 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500">
          </div>

          <!-- 대표전화 -->
          <div>
            <label for="usr_jo" class="block font-medium text-gray-700">대표전화:</label>
            <input type="number" id="usr_jo" v-model="companyInfo.usr_jo" required
                  class="mt-1 block w-full px-4 py-1 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500">
          </div>

          <!-- 비밀번호 -->
          <div class="col-span-2">
            <label for="confirm_usr_pwd" class="block font-medium text-gray-700">비밀번호 확인:</label>
            <input type="password" id="confirm_usr_pwd" v-model="confirmPassword" required
                  class="mt-1 block w-full px-4 py-1 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500">
          </div>
        </div>

        <div class="flex justify-center mt-6">
          <button type="submit" class="inline-flex justify-center py-1 px-4 border border-transparent shadow-lg text-lg font-medium rounded-md text-white bg-green-600 hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500">
            저장
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { reactive, ref } from 'vue'
import store from '@/store/index.js'

export default {
  name: 'CompanyInfo',
  setup () {
    const usrId = store.state.usrInfo.usrId
    const companyInfo = reactive({
      usrId: store.state.usrInfo.usrId,
      usr_com: '',
      usr_name: '',
      usr_corp: '',
      usr_addr: '',
      usr_dti: '',
      usr_type: '',
      usr_jo: 0
    })
    const password = ref('')
    const confirmPassword = ref('')
    const passwordConfirmed = ref(false)
    const updateSuccess = ref(false)

    // 비밀번호 확인
    const verifyPassword = async () => {
      try {
        const response = await axios.post('/api/company/confirm-password', { usrId, usr_pwd: password.value })
        if (response.data.success) {
          passwordConfirmed.value = true
          fetchCompanyInfo()
        } else {
          alert('비밀번호가 일치하지 않습니다.')
        }
      } catch (error) {
        console.error('비밀번호 확인 중 오류 발생:', error)
      }
    }

    // 업체 정보 불러오기
    const fetchCompanyInfo = async () => {
      try {
        const response = await axios.get(`/api/company/${usrId}`)
        Object.assign(companyInfo, response.data)
      } catch (error) {
        console.error('업체 정보를 불러오는 중 오류 발생:', error)
      }
    }

    // 업체 정보 업데이트
    const updateCompanyInfo = async () => {
      if (!confirm('정보를 저장하시겠습니까?')) return
      try {
        await axios.put(`/api/company/${usrId}`, { ...companyInfo, confirmPassword: confirmPassword.value })
        updateSuccess.value = true
        alert('저장되었습니다.')
      } catch (error) {
        alert('비밀번호가 일치하지 않습니다.')
        console.error('업체 정보를 업데이트하는 중 오류 발생:', error)
      }
    }

    // 주소 검색 창 열기
    const openAddressSearch = () => {
      execDaumPostcode()
    }

    const execDaumPostcode = () => {
      new window.daum.Postcode({
        oncomplete: (data) => {
          if (data.userSelectedType === 'R') {
            companyInfo.usr_addr = data.roadAddress
          } else {
            companyInfo.usr_addr = data.jibunAddress
          }
          companyInfo.usr_dti = data.userSelectedType === 'R' ? `(${data.bname})` : ''
          companyInfo.postcode = data.zonecode
        }
      }).open()
    }

    return {
      companyInfo,
      password,
      confirmPassword,
      passwordConfirmed,
      verifyPassword,
      updateCompanyInfo,
      updateSuccess,
      openAddressSearch
    }
  }
}
</script>

