<template>
  <div class="modal" v-if="props.visible">
    <div class="overlay bg-black opacity-50 fixed inset-0 z-10"></div>
    <div class="modal-card bg-white p-6 mx-auto mt-16 rounded shadow-lg z-20 max-w-lg w-full">
      <button class="close-button absolute top-3 right-3 text-xl" @click="emit('close')">X</button>
      <h2 class="text-center text-xl font-semibold mb-4">{{ currentView === 'list' ? '배송지 선택' : '배송지 추가' }}</h2>

      <!-- 배송지 목록 표시 -->
      <div v-if="currentView === 'list'">
        <div class="delivery-address-list overflow-y-auto max-h-96">
          <div class="delivery-address-item border-b border-gray-300 py-2" v-for="address in addresses"
            :key="address.id">
            <div class="delivery-info flex justify-between items-center w-96">
              <div class="w-3/4">
                <div class="delivery-name font-semibold">{{ address.name }}</div>
                <div class="delivery-details text-sm">
                  <span class="font-bold">{{ address.dv_nam }}</span>&nbsp;
                  <span v-if="address.dv_mus === 'Y'" class="inline-block border border-slate-400 rounded-full px-1 text-xs">기본 배송지</span><br>
                  {{ address.dv_als }}<br>
                  {{ address.dv_adr }}, {{ address.dv_dti }}<br>
                  {{ address.dv_phn }}
                </div>
              </div>
            </div>
            <div class="w-1/4 flex justify-between items-center">
              <button class="text-white bg-blue-500 hover:bg-blue-700 rounded px-4 py-1" @click="emit('select', address)">선택</button>
              <button class="text-white bg-green-500 hover:bg-green-700 rounded px-4 py-1" @click="editAddress(address)">수정</button>
            </div>
          </div>
        </div>
        <button class="add-delivery-address mt-4 bg-blue-500 hover:bg-blue-700 text-white rounded px-6 py-2" @click="changeView('add')">+ 배송지 추가</button>
      </div>

      <!-- 새 배송지 추가 폼 -->
      <div v-else-if="currentView === 'add'">
        <form @submit.prevent="submitNewAddress" class="space-y-4">
          <input v-model="newAddress.dv_dnm" placeholder="배송지명" required class="w-full px-4 py-2 border rounded">
          <input v-model="newAddress.dv_als" placeholder="별칭" required class="w-full px-4 py-2 border rounded">
          <input v-model="newAddress.dv_adr" placeholder="주소" required class="w-96 px-4 py-2 border rounded">
          <button type="button" @click="openAddressSearch" class="mt-1 bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-r-md">
              검색
            </button>
          <input v-model="newAddress.dv_dti" placeholder="상세주소" required class="w-80 px-4 py-2 border rounded">
          <input v-model="newAddress.dv_phn" placeholder="연락처" required class="w-full px-4 py-2 border rounded">
          <input v-model="newAddress.dv_nam" placeholder="주문자명" required class="w-full px-4 py-2 border rounded">
          <label class="block">
            <input type="checkbox" v-model="newAddress.dv_mus" class="mr-2"> 기본 배송지로 설정
          </label>
          <button type="submit" class="w-full bg-blue-500 hover:bg-blue-700 text-white rounded py-2">배송지 추가</button>
        </form>
        <button @click="changeView('list')" class="mt-4 w-full bg-gray-500 hover:bg-gray-700 text-white rounded py-2">목록으로 돌아가기</button>
      </div>

      <!-- 배송지 수정 폼 -->
      <div v-if="currentView === 'update'">
        <h2 class="text-xl font-semibold mb-4">배송지 정보 수정</h2>
        <form @submit.prevent="submitEditAddress" class="space-y-4">
          <input v-model="editingAddress.dv_dnm" placeholder="배송지명" required class="w-full px-4 py-2 border rounded">
          <input v-model="editingAddress.dv_als" placeholder="별칭" class="w-full px-4 py-2 border rounded">
          <input v-model="editingAddress.dv_adr" placeholder="주소" required class="w-96 px-4 py-2 border rounded">
          <button type="button" @click="openAddressSearch" class="mt-1 bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-r-md">
              검색
            </button>
          <input v-model="editingAddress.dv_dti" placeholder="상세주소" class="w-80 px-4 py-2 border rounded">
          <input v-model="editingAddress.dv_phn" placeholder="연락처" required class="w-full px-4 py-2 border rounded">
          <label class="block">
            <input type="checkbox" v-model="editingAddress.dv_mus" class="mr-2"> 기본 배송지로 설정
          </label>
          <button type="submit" class="w-full bg-sky-500 hover:bg-sky-700 text-white rounded py-2">수정 완료</button>
        </form>
          <button @click="handleDelete(editingAddress.dv_num)" class="w-full bg-slate-100 hover:bg-slate-400 text-red rounded py-2">삭제</button>
      </div>
    </div>
  </div>
</template>

<script setup>
import { defineProps, defineEmits, ref, reactive } from 'vue'
import axios from 'axios'
import store from '@/store'

const props = defineProps({
  visible: Boolean,
  addresses: Array
})

const emit = defineEmits(['close', 'select', 'updated'])
const usrId = store.state.usrInfo.usrId
const currentView = ref('list')
const editingAddress = ref(null)
const newAddress = reactive({
  usrId,
  dv_dnm: '',
  dv_als: '',
  dv_adr: '',
  dv_dti: '',
  dv_phn: '',
  dv_nam: '',
  dv_mus: false
})

const changeView = view => { currentView.value = view }

const editAddress = address => {
  editingAddress.value = { ...address }
  editingAddress.value.dv_mus = address.dv_mus === 'Y'
  currentView.value = 'update'
}

const submitNewAddress = async () => {
  try {
    const newAddressData = {
      dv_id: usrId,
      dv_dnm: newAddress.dv_dnm,
      dv_als: newAddress.dv_als,
      dv_adr: newAddress.dv_adr,
      dv_dti: newAddress.dv_dti,
      dv_phn: newAddress.dv_phn,
      dv_nam: newAddress.dv_nam,
      dv_mus: newAddress.dv_mus ? 'Y' : 'N'
    }

    await axios.post('/api/delivery-addresses/add', newAddressData)
    emit('updated')
    Object.keys(newAddress).forEach(key => { newAddress[key] = '' })
    alert('배송지 정보가 추가되었습니다.')
    changeView('list')
  } catch (error) {
    console.error('배송지 추가 중 오류 발생:', error)
    alert('배송지 추가 중 오류가 발생했습니다.')
  }
}

const submitEditAddress = async () => {
  const addressData = {
    ...editingAddress.value,
    usrId: store.state.usrInfo.usrId,
    dv_mus: editingAddress.value.dv_mus ? 'Y' : 'N'
  }
  try {
    await axios.put(`/api/delivery-addresses/update/${editingAddress.value.dv_num}`, addressData)
    alert('배송지 정보가 수정되었습니다.')
    emit('updated')
    editingAddress.value = null
    currentView.value = 'list'
  } catch (error) {
    console.error('배송지 수정 중 오류 발생:', error)
    alert('배송지 수정 중 오류가 발생했습니다.')
  }
}

const handleDelete = async (dvNum) => {
  if (!confirm('이 배송지를 삭제하시겠습니까?')) return
  try {
    await axios.delete(`/api/delivery-addresses/delete/${dvNum}`)
    alert('배송지 정보가 삭제되었습니다.')
    emit('updated')
  } catch (error) {
    console.error('배송지 삭제 중 오류 발생:', error)
    alert('배송지 삭제 중 오류가 발생했습니다.')
  }
}

// 주소 검색 창 열기
const openAddressSearch = () => {
  execDaumPostcode()
}

const execDaumPostcode = () => {
  new window.daum.Postcode({
    oncomplete: (data) => {
      if (currentView.value === 'update') {
        if (data.userSelectedType === 'R') {
          editingAddress.value.dv_adr = data.roadAddress
        } else {
          editingAddress.value.dv_adr = data.jibunAddress
        }
      } else {
        if (data.userSelectedType === 'R') {
          newAddress.dv_adr = data.roadAddress
        } else {
          newAddress.dv_adr = data.jibunAddress
        }
      }
      newAddress.dv_dti = data.userSelectedType === 'R' ? `(${data.bname})` : ''
      editingAddress.value.dv_dti = data.userSelectedType === 'R' ? `(${data.bname})` : ''
      newAddress.postcode = data.zonecode
    }
  }).open()
}
</script>

<style scoped>
.modal,
.overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10;
}

.overlay {
  opacity: 0.5;
  background-color: black;
}

.modal-card {
  position: relative;
  max-width: 600px;
  margin: auto;
  margin-top: 100px;
  padding: 20px;
  background-color: #f6f6f6;
  min-height: 700px;
  z-index: 11;
  border: 1px solid black;
  opacity: 1;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 1.5em;
  cursor: pointer;
}

.delivery-address-list {
  max-height: 600px;
  overflow-y: auto;
}

.delivery-address-item {
  border-bottom: 1px solid #ccc;
  padding: 10px;
  display: flex;
  justify-content: space-between;
}

.delivery-select button {
  padding: 5px 10px;
  margin: 0 5px;
  cursor: pointer;
}

.add-delivery-address button {
  padding: 5px 15px;
  cursor: pointer;
}
</style>
