<template>
  <div class='content'>
    <div class="mx-4 my-5 h-full">
      <h1 class='text-2xl font-bold mb-4'><i class="fa-solid fa-credit-card"></i> 주문내역</h1>

      <!-- 날짜 입력란 및 버튼 컨테이너 -->
      <div class="flex items-center mb-5">
        <input type="date" v-model="startDate"
          class="border border-gray-300 rounded px-2 py-1 text-gray-700 leading-tight focus:outline-none focus:border-blue-500" />
        <span class="mx-2">~</span>
        <input type="date" v-model="endDate"
          class="border border-gray-300 rounded px-2 py-1 text-gray-700 leading-tight focus:outline-none focus:border-blue-500" />
        <button @click="setDateRange('today')"
          class="ml-4 bg-blue-500 hover:bg-blue-700 text-white font-bold py-1 px-3 rounded focus:outline-none focus:shadow-outline">오늘</button>
        <button @click="setDateRange('week')"
          class="ml-2 bg-blue-500 hover:bg-blue-700 text-white font-bold py-1 px-3 rounded focus:outline-none focus:shadow-outline">최근 1주일</button>
        <button @click="setDateRange('month')"
          class="ml-2 bg-blue-500 hover:bg-blue-700 text-white font-bold py-1 px-3 rounded focus:outline-none focus:shadow-outline">최근 3개월</button>
      </div>

      <!-- 검색어 및 체크박스 필터링 -->
      <div class="flex items-center mb-4">
        <select v-model="searchTarget"
          class="border border-gray-300 rounded px-2 py-1 text-gray-700 leading-tight focus:outline-none focus:border-blue-500 mr-2">
          <option value="item_name">품목명</option>
          <option value="order_number">주문번호</option>
          <option value="item_code">품목코드</option>
        </select>

        <input type="text"
          class="border border-gray-300 rounded px-2 py-1 text-gray-700 leading-tight focus:outline-none focus:border-blue-500 mr-2 w-1/4"
          id="filter-text-box" placeholder="검색어를 입력해주세요" v-model="searchKeyword" @keyup.enter="search">
        <button class="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-5 rounded mr-2"
          @click="search">검색</button>
        <div class="flex space-x-80">
          <div class="flex items-center">
            <div v-for="status in orderStatuses" :key="status.value" class="mr-3">
              <label :for="status.value"
                class="inline-flex items-center cursor-pointer border border-gray-300 rounded px-3 py-1 hover:bg-gray-100 shadow">
                <input type="checkbox" :id="status.value" :value="status.value" v-model="selectedStatuses"
                  class="form-checkbox h-5 w-5 text-blue-600" @change="search">
                <span class="ml-2 text-gray-700">{{ status.text }}</span>
              </label>
            </div>
          </div>
          <div class="mx-auto space-x-2">
            <button class="bg-red-500 hover:bg-red-700 text-white font-bold py-2 px-4 rounded"
              @click="exportToPDF(gridApi)"><i class="fa-solid fa-file-pdf"></i> PDF</button>
            <button class="bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-4 rounded"
              @click="onBtnExport()"><i class="fa-solid fa-file-csv"></i> CSV</button>
          </div>
        </div>
      </div>

      <!-- AG Grid -->
      <div class='flex mx-1 h-full'>
        <div class='w-full'>
          <ag-grid-vue class='ag-theme-alpine' style='width: 100%; height: 600px;' :columnDefs='colDefs'
            :rowData='processedRowData' rowSelection='multiple' suppressRowClickSelection='true' alwaysShowVerticalScroll='true'
            :defaultColDef='defaultColDef' @grid-ready="onGridReady">
          </ag-grid-vue>
        </div>
      </div>

      <!-- 주문 상세 내역 모달 -->
      <OrderDetailModal :visible="isModalVisible" :orderDetails="selectedOrderDetails" @close="closeModal" />
    </div>
  </div>
</template>

<script>
import { ref, onMounted, computed, watch } from 'vue'
import 'ag-grid-community/styles/ag-grid.css'
import 'ag-grid-community/styles/ag-theme-alpine.css'
import { exportToPDF } from '../script/gridExporter'
import { AgGridVue } from 'ag-grid-vue3'
import axios from 'axios'
import OrderDetailModal from '@/components/modal/OdrDtil.vue'
import store from '@/store'

export default {
  name: 'OrderHistoryView',
  components: {
    AgGridVue,
    OrderDetailModal
  },
  setup () {
    const today = new Date().toISOString().substring(0, 10)
    const startDate = ref(today)
    const endDate = ref(today)
    const allData = ref([]) // 전체 데이터
    const rowData = ref([]) // 필터링된 데이터
    const searchTarget = ref('item_name') // 검색 대상 (품목명, 주문번호, 품목코드)
    const searchKeyword = ref('') // 검색어
    const selectedStatuses = ref(['all']) // 선택된 주문 상태
    const gridApi = ref(null) // AG Grid API
    const isModalVisible = ref(false) // 모달 표시 여부
    const selectedOrderDetails = ref([]) // 선택된 주문 상세 내용

    const calculatePaymentTotal = () => {
      const paymentTotals = {}

      rowData.value.forEach((order) => {
        if (!paymentTotals[order.odr_id]) {
          paymentTotals[order.odr_id] = 0
        }
        paymentTotals[order.odr_id] += parseFloat(order.ods_tot)
      })

      return paymentTotals
    }

    const processedRowData = computed(() => {
      const paymentTotals = calculatePaymentTotal()

      return rowData.value.map(order => ({
        ...order,
        payment_total: parseFloat((paymentTotals[order.odr_id] * 1.1).toFixed(2))
      }))
    })

    // AG Grid 준비 완료 시 호출되는 함수
    const onGridReady = (params) => {
      gridApi.value = params.api
    }

    // CSV 파일로 내보내기
    const onBtnExport = () => {
      const params = {
        processCellCallback: function (params) {
          if (params.column.colId === 'odr_id') {
            return `'${params.value} `
          }
          if (params.column.colId === 'odr_sts') {
            switch (params.value) {
              case 0: return '주문취소'
              case 1: return '취소완료'
              case 2: return '입금완료'
              case 3: return '배송완료'
              case 4: return '계산서발행완료'
              default: return ''
            }
          }
          return params.value
        },
        processHeaderCallback: function (params) {
          if (params.column.colId === 'payment_total') {
            return '결재금액\n(부가세 포함)'
          }
          return params.column.colDef.headerName || params.column.colId
        },
        columnKeys: colDefs.value.filter(col => col.field !== 'detail_button').map(col => col.field)
      }

      if (gridApi.value) {
        const csvContent = gridApi.value.getDataAsCsv(params)
        const csvBlob = new Blob([`\uFEFF조회 기간: ${startDate.value} ~ ${endDate.value}\n` + csvContent], { type: 'text/csv;charset=utf-8;' })
        const csvUrl = URL.createObjectURL(csvBlob)
        const hiddenElement = document.createElement('a')
        hiddenElement.href = csvUrl
        hiddenElement.target = '_blank'
        hiddenElement.download = 'data.csv'
        hiddenElement.click()
      }
    }

    // 주문 취소 버튼 렌더러
    const cancelOrderCellRenderer = (params) => {
      const button = document.createElement('button')
      button.className = 'text-black focus:outline-none font-bold px-4 rounded'
      button.disabled = true

      // 주문 상태에 따라 버튼 텍스트 및 스타일 변경
      switch (params.data.odr_sts) {
        case 0:
          button.textContent = '주문취소'
          button.className += ' bg-red-500 text-white hover:bg-red-700'
          button.disabled = false
          button.addEventListener('click', () => {
            if (confirm(`주문번호 ${params.data.odr_id}를 취소하시겠습니까?`)) {
              axios.post('/api/order-cancel', { odr_id: params.data.odr_id })
                .then(response => {
                  alert('주문이 취소되었습니다.')
                  fetchData()
                })
                .catch(error => {
                  console.error('주문 취소 중 오류가 발생했습니다.', error)
                  alert('주문 취소 중 오류가 발생했습니다.')
                })
            }
          })
          break
        case 1:
          button.textContent = '취소완료'
          button.className += ' text-grey-400'
          break
        case 2:
          button.textContent = '입금완료'
          button.className += ' text-green-400'
          break
        case 3:
          button.textContent = '배송완료'
          button.className += ' text-blue-400'
          break
        case 4:
          button.textContent = '계산서발행완료'
          button.className += ' text-gray-400'
          break
      }

      return button
    }

    // 주문 상세 버튼 렌더러
    const orderDetailButtonRenderer = (params) => {
      const button = document.createElement('button')
      button.className = 'ml-2 bg-blue-500 hover:bg-blue-700 text-white font-bold px-2 rounded'
      button.textContent = '주문상세내역'
      button.addEventListener('click', () => {
        showOrderDetails(params.data.odr_id)
      })

      return button
    }

    // 주문번호 셀 병합을 위한 함수
    function calculateRowSpan (params) {
      const currentOdrId = params.data.odr_id
      const currentIndex = params.node.rowIndex
      return currentIndex === 0 || rowData.value[currentIndex - 1].odr_id !== currentOdrId
        ? rowData.value.filter((e) => e.odr_id === currentOdrId).length
        : 0
    }

    // AG Grid 컬럼 정의
    const colDefs = ref([
      { headerName: '주문일자', field: 'odr_dat', minWidth: 100, maxWidth: 120, sortable: true, cellStyle: { 'text-align': 'center' }, rowSpan: calculateRowSpan },
      {
        headerName: '주문번호',
        field: 'odr_id',
        minWidth: 230,
        maxWidth: 250,
        sortable: true,
        cellStyle: { 'text-align': 'center' },
        rowSpan: calculateRowSpan,
        cellClassRules: { 'show-cell': 'value !== undefined' }
      },
      { headerName: '품목코드', field: 'ods_gub', minWidth: 100, maxWidth: 110, sortable: true, cellStyle: { 'text-align': 'center' } },
      { headerName: '품목명', field: 'ods_nam', minWidth: 150, maxWidth: 180, sortable: true },
      { headerName: '규격', field: 'gub_spc', minWidth: 60, maxWidth: 100, sortable: true, cellStyle: { 'text-align': 'right' } },
      { headerName: '단위', field: 'gub_unt', minWidth: 60, maxWidth: 100, sortable: true, cellStyle: { 'text-align': 'center' } },
      { headerName: '단가', field: 'ods_prc', minWidth: 60, maxWidth: 100, sortable: true, cellStyle: { 'text-align': 'right' }, valueFormatter: numberWithCommas },
      { headerName: '주문수량', field: 'ods_qty', minWidth: 60, maxWidth: 120, sortable: true, cellStyle: { 'text-align': 'right' }, valueFormatter: numberWithCommas },
      { headerName: '합계금액', field: 'ods_tot', minWidth: 80, maxWidth: 150, sortable: true, cellStyle: { 'text-align': 'right' }, valueFormatter: numberWithCommas },
      {
        headerComponentParams: {
          template: '<div style="text-align:center; white-space: nowrap;">결재금액<br>(부가세 포함)</div>'
        },
        field: 'payment_total',
        minWidth: 80,
        maxWidth: 130,
        sortable: true,
        cellStyle: { 'text-align': 'right' },
        valueFormatter: numberWithCommas,
        rowSpan: calculateRowSpan,
        cellClassRules: { 'show-cell': 'value !== undefined' }
      },
      {
        headerName: '배송요청일',
        field: 'odr_drd',
        minWidth: 100,
        maxWidth: 120,
        sortable: true,
        cellStyle: { 'text-align': 'center' },
        rowSpan: calculateRowSpan,
        cellClassRules: { 'show-cell': 'value !== undefined' }
      },
      {
        headerName: '처리상태',
        field: 'odr_sts',
        minWidth: 140,
        maxWidth: 160,
        sortable: true,
        cellRenderer: cancelOrderCellRenderer,
        cellStyle: { 'text-align': 'center' },
        rowSpan: calculateRowSpan,
        cellClassRules: { 'show-cell': 'value !== undefined' }
      },
      {
        headerName: '주문상세내역',
        field: 'detail_button',
        minWidth: 150,
        maxWidth: 160,
        cellStyle: { 'text-align': 'center' },
        cellRenderer: orderDetailButtonRenderer
      }
    ])

    const defaultColDef = ref({
      sortable: true
    })

    // 날짜 형식 변환 함수
    const formatDate = (dateStr) => {
      return dateStr.replace(/(\d{4})(\d{2})(\d{2})/, '$1-$2-$3')
    }

    const orderStatuses = [
      { text: '전체', value: 'all' },
      { text: '주문완료', value: '0' },
      { text: '입금완료', value: '2' },
      { text: '배송완료', value: '3' }
    ]

    // 필터링된 데이터 계산
    const filteredData = computed(() => {
      let filtered = allData.value

      // 상태 필터링
      if (!selectedStatuses.value.includes('all') && selectedStatuses.value.length > 0) {
        filtered = filtered.filter(order => selectedStatuses.value.includes(order.odr_sts))
      }

      return filtered
    })

    // 데이터 불러오기 함수
    const fetchData = async () => {
      try {
        const response = await axios.get('/api/order-history', { params: { usrId: store.state.usrInfo.usrId } })
        allData.value = response.data.map(order => ({
          ...order,
          odr_dat: formatDate(order.odr_dat),
          odr_sts: order.odr_sts
        }))
        rowData.value = filteredData.value
      } catch (error) {
        console.error('주문 내역을 불러오는 중 오류 발생:', error)
      }
    }

    // 데이터 변경 시 rowData 업데이트
    watch(filteredData, (newData) => {
      rowData.value = newData
    }, { immediate: true })

    // 상태 변경 시 선택된 상태 업데이트
    watch(selectedStatuses, (newValues, oldValues) => {
      if (newValues.includes('all') && newValues.length > 1) {
        selectedStatuses.value = newValues.filter(value => value !== 'all')
      } else if (newValues.length === 0) {
        selectedStatuses.value = ['all']
      }
    })

    onMounted(fetchData)

    // 날짜 범위 설정 함수
    const setDateRange = (range) => {
      const today = new Date()
      endDate.value = today.toISOString().substring(0, 10)

      if (range === 'today') {
        startDate.value = endDate.value
      } else if (range === 'week') {
        const weekAgo = new Date(new Date().setDate(today.getDate() - 7))
        startDate.value = weekAgo.toISOString().substring(0, 10)
      } else if (range === 'month') {
        const monthAgo = new Date(new Date().setMonth(today.getMonth() - 3))
        startDate.value = monthAgo.toISOString().substring(0, 10)
      }
    }

    // 검색 함수
    const search = () => {
      let filteredData = allData.value.filter(order => {
        const orderDate = new Date(order.odr_dat)
        const start = new Date(startDate.value)
        const end = new Date(endDate.value)
        return orderDate >= start && orderDate <= end
      })

      if (searchKeyword.value) {
        filteredData = filteredData.filter(order => {
          if (searchTarget.value === 'item_name') {
            return order.ods_nam.toLowerCase().includes(searchKeyword.value.toLowerCase())
          } else if (searchTarget.value === 'order_number') {
            return order.odr_id.toLowerCase().includes(searchKeyword.value.toLowerCase())
          } else if (searchTarget.value === 'item_code') {
            return order.ods_gub.toLowerCase().includes(searchKeyword.value.toLowerCase())
          }
          return true
        })
      }

      if (!selectedStatuses.value.includes('all') && selectedStatuses.value.length > 0) {
        filteredData = filteredData.filter(order => selectedStatuses.value.includes(order.odr_sts))
      }

      rowData.value = filteredData
    }

    // 숫자 형식 변환 함수
    function numberWithCommas (params) {
      return params.value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
    }

    // 주문 상세 내역 버튼 클릭 핸들러
    const showOrderDetails = async (orderId) => {
      try {
        const response = await axios.get(`/api/order-details/${orderId}`)
        selectedOrderDetails.value = response.data
        isModalVisible.value = true
      } catch (error) {
        console.error('주문 상세 내역을 불러오는 중 오류 발생:', error)
      }
    }

    const closeModal = () => {
      isModalVisible.value = false
    }

    return {
      rowData,
      colDefs,
      defaultColDef,
      searchTarget,
      search,
      searchKeyword,
      startDate,
      endDate,
      setDateRange,
      fetchData,
      selectedStatuses,
      orderStatuses,
      filteredData,
      gridApi,
      onBtnExport,
      onGridReady,
      exportToPDF: () => exportToPDF(gridApi.value, startDate.value, endDate.value),
      processedRowData,
      isModalVisible,
      selectedOrderDetails,
      showOrderDetails,
      closeModal
    }
  }
}
</script>

<style>
.show-cell {
  display: table-cell !important;
}
</style>
