<template>
  <div class="fixed inset-0 z-10 overflow-y-auto" v-if="props.visible">
    <div class="fixed inset-0 bg-gray-600 bg-opacity-50"></div>
    <div class="relative top-20 mx-auto p-5 border w-1/4 shadow-lg rounded-md bg-white">
      <button class="absolute top-3 right-3 text-xl" @click="closeModal">X</button>
      <h2 class="text-center text-xl font-semibold mb-6">주문상세</h2>

      <div class="mb-4">
        <p class="text-sm text-gray-600">{{ formatDate(orderDetails[0].odr_dat) }} 주문 · 주문번호: {{ orderDetails[0].odr_id }}</p>
      </div>

      <div class="border-t border-b py-4 mb-6">
        <div class="flex items-center justify-between">
          <div>
            <span class="text-green-600">{{ getOrderStatus(orderDetails[0].odr_sts) }}</span>
            <span v-if="orderDetails[0].odr_sts !== 0 && orderDetails[0].odr_sts !== 1"> · {{ formatDate1(orderDetails[0].odr_drd) }} 도착</span>
          </div>
        </div>
        <div class="flex items-center py-4" v-for="detail in orderDetails" :key="detail.ods_gub">
          <img class="w-24 h-24 object-cover mr-4" :src="`/img/item/${detail.img_itm}`" alt="Product Image">
          <div>
            <p class="text-sm text-gray-800">{{ detail.ods_nam }}</p>
            <p class="text-sm text-gray-600">{{ parseInt(detail.ods_prc).toLocaleString() }} 원 · {{ parseInt(detail.ods_qty).toLocaleString() }}개</p>
          </div>
        </div>
        <div class="flex justify-end space-x-4">
          <button class="bg-yellow-500 text-white rounded px-4 py-2 hover:bg-yellow-700" @click="addOrderDetailsToCart">장바구니 담기</button>
        </div>
      </div>

      <div class="mb-6">
        <h3 class="text-lg font-semibold mb-2">받는사람 정보</h3>
        <div class="text-sm text-gray-800">
          <p>받는사람: {{ orderDetails[0].odr_com }}</p>
          <p>연락처: {{ orderDetails[0].odr_tel }}</p>
          <p>받는주소: {{ orderDetails[0].odr_del }}, {{ orderDetails[0].odr_dti }}</p>
          <p>배송요청사항: {{ orderDetails[0].odr_txt }}</p>
        </div>
      </div>

      <div>
        <h3 class="text-lg font-semibold mb-2">결제 정보</h3>
        <div class="text-sm text-gray-800 mb-4">
          <p>결제수단: 계좌입금</p>
        </div>
        <div class="text-sm text-gray-800">
          <div class="flex justify-between">
            <span>총 상품가격</span>
            <span>{{ totalProductPrice.toLocaleString() }} 원</span>
          </div>
          <div class="flex justify-between">
            <span>부가세</span>
            <span>{{ taxAmount.toLocaleString() }} 원</span>
          </div>
          <div class="flex justify-between">
            <span>배송비</span>
            <span>{{ shippingFee.toLocaleString() }} 원</span>
          </div>
          <div class="flex justify-between border-t mt-2 pt-2 font-semibold">
            <span>총 결제금액</span>
            <span>{{ totalPayment.toLocaleString() }} 원</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { defineProps, defineEmits, computed } from 'vue'
import axios from 'axios'

const props = defineProps({
  visible: Boolean,
  orderDetails: Array
})

const emit = defineEmits(['close'])

const closeModal = () => {
  emit('close')
}

const formatDate = (dateStr) => {
  const year = dateStr.substring(0, 4)
  const month = dateStr.substring(4, 6)
  const day = dateStr.substring(6, 8)
  return `${year}. ${month}. ${day}`
}

const formatDate1 = (dateStr) => {
  const [year, month, day] = dateStr.split('-')
  return `${year}. ${month}. ${day}`
}

// odr_sts 값에 따른 주문 상태 반환
const getOrderStatus = (status) => {
  switch (status) {
    case 0:
      return '주문완료'
    case 1:
      return '환불완료'
    case 2:
      return '배송준비중'
    case 3:
      return '배송완료'
    case 4:
      return '배송완료'
    default:
      return '상태 불명'
  }
}

// 상품 총 가격 계산
const totalProductPrice = computed(() => {
  return props.orderDetails.reduce((total, detail) => {
    return total + parseInt(detail.ods_tot, 10)
  }, 0)
})

// 부가세 계산
const taxAmount = computed(() => {
  return Math.floor(totalProductPrice.value * 0.1)
})

// 배송비 계산
const shippingFee = computed(() => {
  return totalProductPrice.value < 20000 ? 3000 : 0
})

// 총 결제 금액 계산
const totalPayment = computed(() => {
  return totalProductPrice.value + taxAmount.value + shippingFee.value
})

// 통합된 addToCart 함수
const addToCart = async (products) => {
  const selectedProducts = products.filter(item => item.ord_qty > 0)
  if (selectedProducts.length === 0) {
    alert('장바구니에 추가할 상품을 선택해주세요.')
    return
  }
  try {
    await axios.post('/api/scmusr/updateorder2', selectedProducts)
    alert('상품이 장바구니에 추가되었습니다.')
    if (confirm('품목을 담았습니다. 장바구니로 이동하시겠습니까?')) {
      window.location.href = '/contract/ordercart'
    }
    selectedProducts.forEach(product => { product.ord_qty = 0 })
  } catch (error) {
    console.error('장바구니 추가 중 에러 발생:', error)
    alert('장바구니에 추가하는 중 에러가 발생했습니다.')
  }
}

// 주문 상세에서 장바구니에 담기
const addOrderDetailsToCart = () => {
  const productsToCart = props.orderDetails.map(detail => ({
    ...detail,
    ord_qty: detail.ods_qty // 기존 주문 수량을 ord_qty로 설정
  }))
  addToCart(productsToCart)
}
</script>

<style scoped>
.modal {
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
