<template>
  <div class="content">
    <!-- 페이지 제목 -->
    <h1 class="text-2xl font-bold" style="margin: 15px -25px -35px 25px;">주문결제</h1>
    <!-- 그리드 호출 부분 -->
    <div class="flex mx-1 my-20 h-full">
      <div class="w-4/5">
        <div class="flex flex-row-reverse">
        </div>
        <ag-grid-vue class="ag-theme-alpine" style="width: 100%; height: 550px;" :columnDefs="colDefs"
          :rowData="rowData.value" rowSelection="multiple" suppressRowClickSelection="true" :domLayout="'autoHeight'"
          :defaultColDef="defaultColDef" @grid-ready="onGridReady" @selection-changed="onSelectionChanged">
        </ag-grid-vue>
      </div>
      <!-- 결제 예정금액 -->
      <div class="ml-4 border border-gray-400 p-4 rounded" style="width: 15%; height: 50%;">
        <h1 class="mt-4 font-black mb-7 text-xl">결제 예정금액</h1>
        <div class="flex justify-between mb-4">
          <h2 class="pr-10">총 품목 금액</h2>
          <h2 class="font-bold">{{ totalItemAmount }}원</h2>
        </div>
        <div class="flex justify-between mb-4">
          <h2 class="pr-10">총 주문수량</h2>
          <h2 class="font-bold">{{ totalQuantity }}개</h2>
        </div>
        <div class="flex justify-between mb-4">
          <h2 class="pr-10">배송비+</h2>
          <h2 class="font-bold">{{ shippingFee }}원</h2>
        </div>
        <hr class="my-3">
        <div class="flex justify-between">
          <h2>합계</h2>
          <h2 class="font-bold">{{ totalAmount }}원</h2>
        </div>
        <!-- 주문 정보 입력 폼 -->
        <div class="mt-10">
          <h2 class="font-bold">주문 정보 입력</h2>
          <div>
            <label for="recipient">받는 사람</label>
            <input type="text" id="recipient" v-model="recipient">
          </div>
          <div>
            <label for="postcode">우편번호</label>
            <input type="text" id="postcode" v-model="postcode">
            <button @click="findAddress">주소찾기</button>
          </div>
          <div>
            <label for="address">기본 주소</label>
            <input type="text" id="address" v-model="address">
          </div>
          <div>
            <label for="detailAddress">상세 주소 및 건물명</label>
            <input type="text" id="detailAddress" v-model="detailAddress">
          </div>
          <div>
            <label for="phoneNumber">휴대폰 입력</label>
            <input type="text" id="phoneNumber" v-model="phoneNumber">
          </div>
          <div>
            <label for="deliveryRequest">배송시 요청사항</label>
            <select id="deliveryRequest" v-model="deliveryRequest">
              <option value="">선택하세요</option>
              <option value="문 앞에 놓아주세요">문 앞에 놓아주세요</option>
              <option value="택배함에 넣어주세요">택배함에 넣어주세요</option>
              <option value="집 안으로 들어와주세요">집 안으로 들어와주세요</option>
            </select>
          </div>
        </div>
        <!-- 주문하기 버튼 -->
        <button class="bg-gray-800 text-white rounded-md hover:bg-blue-600 mt-10 p-1 w-full h-14"
          @click="placeOrder">주문하기</button>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, ref } from 'vue'
import 'ag-grid-community/styles/ag-grid.css'
import 'ag-grid-community/styles/ag-theme-quartz.css'
import { AgGridVue } from 'ag-grid-vue3'
import axios from 'axios'

export default {
  name: 'ContractHomeView',
  components: {
    AgGridVue
  },
  setup () {
    const rowData = reactive([])
    const totalItemAmount = ref(0) // 총 품목 금액
    const totalAmount = ref(0) // 최종 금액 (품목 금액 + 배송비)
    const totalQuantity = ref(0)
    const shippingFee = ref(0) // 배송비
    const gridApi = ref()

    const colDefs = reactive([
      {
        headerName: '',
        field: 'ord_chk',
        // 전체 행에 대한 체크박스 선택을 활성화
        checkboxSelection: true,
        // 헤더의 체크박스를 통한 전체 선택/해제 기능 활성화
        headerCheckboxSelection: true,
        suppressSizeToFit: true,
        maxWidth: 60
      },
      {
        headerName: '이미지',
        field: 'img_itm',
        width: 80,
        cellRenderer: function (params) {
          const imagePath = `/img/itme/${params.value}`
          return `<img src="${imagePath}" style="width: 75%; height: 100%;">`
        }
      },
      { headerName: '코드', field: 'ods_cod', hide: true, cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 50 },
      { headerName: '구분', field: 'ods_typ', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 80, maxWidth: 80, sortable: true, hide: true },
      { headerName: '품목코드', field: 'ods_gub', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 120, maxWidth: 120, sortable: true, hide: true },
      { headerName: '품목명', field: 'ods_nam', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 200, maxWidth: 200, sortable: true },
      { headerName: '규격', field: 'gub_spc', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 80, maxWidth: 80, sortable: true },
      { headerName: '단위', field: 'gub_unt', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 80, maxWidth: 80, sortable: true },
      { headerName: '박스당수량', field: 'gub_box', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 130, maxWidth: 130, sortable: true },
      { headerName: '품목단가', field: 'ods_prc', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 100, maxWidth: 100, sortable: true },
      { headerName: '주문수량', field: 'ods_qty', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 100, maxWidth: 100, sortable: true },
      { headerName: '주문금액', field: 'ods_tot', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 100, maxWidth: 100, sortable: true },
      { headerName: '주문번호', field: 'ods_oid', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 140, maxWidth: 140, sortable: true, hide: true },
      { headerName: '주문일자', field: 'ods_dat', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 100, maxWidth: 100, sortable: true, hide: true },
      { headerName: 'Seq', field: 'seq', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 90, maxWidth: 90, sortable: true, hide: true }
    ])
    // 페이지가 로드될 때 데이터를 조회합니다.
    const loadData = async () => {
      try {
        const response = await axios.get('/api/scmusr/order')
        rowData.value = response.data
      } catch (error) {
        console.error('데이터 로드 중 에러:', error)
      }
    }
    loadData()

    // 품목 삭제
    const removeSelectedItems = () => {
      const selectedData = gridApi.value.getSelectedRows()

      // 선택된 품목이 없을 경우
      if (selectedData.length === 0) {
        alert('삭제할 품목을 선택해주세요.')
        return // 함수 실행을 여기서 중단
      }

      // 사용자에게 삭제 확인 요청
      const isConfirmed = confirm('선택한 품목을 삭제하시겠습니까?')

      // 사용자가 '확인'을 눌렀을 경우에만 삭제 진행
      if (isConfirmed) {
        const selectedSeqs = selectedData.map(row => row.seq)

        axios.delete('/api/scmusr/deleteOrder', { data: selectedSeqs })
          .then(() => {
            alert('선택한 품목이 삭제되었습니다.')
            loadData() // 성공 시 데이터 재로딩
          })
          .catch(error => console.error('삭제 중 에러 발생:', error))
      }
    }

    // 결재창
    const onSelectionChanged = () => {
      if (gridApi.value) {
        const selectedData = gridApi.value.getSelectedRows()

        // 총 품목 금액 계산 (ods_tot의 합계)
        totalItemAmount.value = selectedData.reduce((acc, row) => acc + parseInt(row.ods_tot, 10), 0)

        // 총수량 계산
        totalQuantity.value = selectedData.reduce((acc, row) => acc + parseInt(row.ods_qty, 10), 0)

        // 배송비 계산: 총 품목 금액이 20,000원 이하일 경우 3,000원, 그렇지 않으면 0원
        shippingFee.value = totalItemAmount.value < 20000 ? 3000 : 0

        // 최종 금액 계산 (품목 금액 + 배송비)
        totalAmount.value = totalItemAmount.value + shippingFee.value
      } else {
        console.error('GridApi가 아직 초기화되지 않았습니다.')
      }
    }
    const onGridReady = (params) => {
      gridApi.value = params.api
    }

    return {
      rowData,
      colDefs,
      onSelectionChanged,
      onGridReady,
      totalAmount,
      totalQuantity,
      totalItemAmount,
      shippingFee,
      removeSelectedItems
    }
  }
}
</script>

<style scoped>
