<template>
  <div class="content flex flex-col md:flex-row p-4 space-y-4 md:space-y-0 md:space-x-4">
    <!-- 일반문의 리스트 영역 -->
    <div class="w-full md:w-2/3">
      <div class="card bg-white shadow-md rounded-lg p-6 mb-4">
        <h1 class="text-2xl font-bold mb-4"><i class="fa-solid fa-headset"></i> 문의게시판</h1>
        <!-- 일반문의 추가 버튼 -->
        <div class="flex justify-end">
          <button @click="addNotice" class="bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline mb-4">일반문의 추가</button>
        </div>
        <!-- 일반문의 목록 표시 -->
        <div class="ag-theme-alpine w-full" style="height: 600px;">
          <ag-grid-vue
            class="ag-theme-alpine w-full h-full"
            :gridOptions="gridOptions"
            :rowData="notices"
            :columnDefs="columnDefs"
            :defaultColDef="{ resizable: true }"
            @firstDataRendered="onFirstDataRendered"
          ></ag-grid-vue>
        </div>
      </div>
    </div>
    <!-- 일반문의 상세 및 댓글 영역 -->
    <div class="w-full md:w-1/3">
      <div v-if="showForm || selectedNotice" class="card bg-white shadow-md rounded-lg p-6" style="height: 90%; overflow-y: auto">
        <!-- 제목 상태 전환 -->
        <div class="mb-8">
          <span class="text-2xl font-bold mb-4 mr-4">
            {{ showForm
              ? (selectedNotice ? '게시글 수정' : '새 게시글 추가')
              : (selectedNotice && selectedNotice.tb_div === 1 ? '공지글 상세보기' : '게시글 상세보기')
            }}
          </span>
          <span :class="[selectedNotice?.tb_div === 1 ? 'bg-red-200 text-red-800' : 'bg-blue-200 text-blue-800', 'text-lg mb-4 p-2 rounded']">
            {{ selectedNotice?.tb_div === 1 ? '공지사항' : '일반문의' }}
          </span>
        </div>
        <!-- 입력 폼 조건 수정 -->
        <form v-if="showForm || selectedNotice && selectedNotice.tb_div !== 1" @submit.prevent="selectedNotice ? confirmUpdateNotice() : saveNewNotice()">
          <input type="text" v-model="formTitle" placeholder="제목 입력" class="mb-2 w-full p-2 border rounded"/>
          <textarea v-model="formContent" placeholder="내용 입력" class="w-full p-2 mb-2 border rounded" rows="6"></textarea>
          <div class="flex justify-end space-x-2">
            <button type="submit" class="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline">{{ selectedNotice ? '수정' : '저장' }}</button>
            <button v-if="selectedNotice" @click.prevent="confirmDeleteNotice" class="bg-red-500 hover:bg-red-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline">삭제</button>
          </div>
        </form>
        <!-- 공지사항일 때 내용만 표시 -->
        <div v-else>
          <input type="text" v-model="formTitle" placeholder="제목 입력" class="mb-2 w-full p-2 border rounded" disabled />
          <textarea v-model="formContent" placeholder="내용 입력" class="w-full p-2 mb-2 border rounded" rows="14" disabled ></textarea>
        </div>
        <!-- 댓글 표시 영역 -->
        <div v-if="selectedNotice" class="mt-4">
          <h2 v-if="selectedNotice.tb_div !== 1" class="text-xl font-bold mb-2">댓글</h2>
          <ul>
            <li v-for="comment in comments" :key="comment.com_id" class="mb-2">
              <div class="card bg-gray-50 p-4 rounded shadow-md">
                <p class="text-gray-800">{{ comment.com_txt }}</p>
                <div class="text-sm text-gray-500 flex justify-between items-center">
                  <span>{{ comment.com_nam }} | {{ comment.com_dat }}</span>
                  <button v-if="selectedNotice.tb_div !== 1" @click="confirmDeleteComment(comment.com_id)" class="text-red-500 ml-2">삭제</button>
                </div>
              </div>
            </li>
          </ul>
          <!-- 댓글 입력 폼 -->
          <form v-if="selectedNotice.tb_div !== 1" @submit.prevent="addComment" class="mt-4">
            <textarea v-model="commentContent" placeholder="댓글 입력" class="w-full p-2 mb-2 border rounded" rows="2"></textarea>
            <div class="flex justify-end">
              <button type="submit" class="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline">댓글 추가</button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import { AgGridVue } from 'ag-grid-vue3'
import axios from 'axios'
import store from '@/store/index.js'

export default {
  name: 'NotifiView',
  components: {
    AgGridVue
  },
  setup () {
    // 상태 변수 선언
    const selectedNotice = ref(null)
    const notices = ref([])
    const comments = ref([])
    const showForm = ref(false)
    const formTitle = ref('')
    const formContent = ref('')
    const commentContent = ref('')
    const gridApi = ref(null)

    // 그리드 컬럼 정의
    const columnDefs = ref([
      { headerName: 'NO', field: 'tb_num', minWidth: 80, maxWidth: 80, sortable: true, cellStyle: { 'text-align': 'center' } },
      {
        headerName: '구분',
        field: 'tb_div',
        minWidth: 120,
        maxWidth: 120,
        sortable: true,
        cellRenderer: params => {
          const div = params.value === 1 ? 'bg-red-200 text-red-800' : 'bg-blue-200 text-blue-800'
          const text = params.value === 1 ? '공지사항' : '일반문의'
          return `<span class="px-2 py-1 rounded ${div}">${text}</span>`
        },
        cellStyle: { 'text-align': 'center' }
      },
      {
        headerName: '제목',
        field: 'tb_tit',
        minWidth: 120,
        maxWidth: 350,
        sortable: true,
        cellRenderer: params => {
          if (params.data.tb_div === 1) {
            return `<span class="text-blue-500">${params.value}</span>`
          }
          return `
            <span class="text-blue-500">${params.value}</span>
            <span class="text-gray-500">[${params.data.comment_count}]</span>
          `
        }
      },
      { headerName: '내용', field: 'tb_txt', minWidth: 120, maxWidth: 350, sortable: true },
      { headerName: '작성일', field: 'tb_dat', minWidth: 120, maxWidth: 180, sortable: true, cellStyle: { 'text-align': 'center' } },
      { headerName: '작성자', field: 'tb_nam', minWidth: 120, maxWidth: 120, sortable: true, cellStyle: { 'text-align': 'center' } }
    ])

    // 그리드 옵션 정의
    const gridOptions = ref({
      onRowClicked: event => {
        selectedNotice.value = event.data
        formTitle.value = event.data.tb_tit
        formContent.value = event.data.tb_txt
        showForm.value = false
        loadComments(event.data.tb_num)
      }
    })

    // 그리드 데이터 렌더링 후 호출
    const onFirstDataRendered = params => {
      gridApi.value = params.api
      gridApi.value.sizeColumnsToFit()
    }

    // 일반문의 데이터 로드
    const loadNotices = async () => {
      try {
        const response = await axios.get('/api/notices', { params: { usrId: store.state.usrInfo.usrId } })
        notices.value = response.data
        updateGrid()
      } catch (error) {
        console.error('Failed to load notices', error)
      }
    }

    // 컴포넌트가 마운트될 때 일반문의 로드
    onMounted(loadNotices)

    // 일반문의 추가 폼 표시
    const addNotice = () => {
      showForm.value = true
      selectedNotice.value = null
      formTitle.value = ''
      formContent.value = ''
    }

    // 새로운 일반문의 저장
    const saveNewNotice = async () => {
      try {
        const newNotice = { tb_tit: formTitle.value, tb_txt: formContent.value, tb_nam: store.state.usrInfo.usrName, tb_div: 0, usrId: store.state.usrInfo.usrId }
        const response = await axios.post('/api/addNotice', newNotice)
        newNotice.tb_num = response.data.tb_num
        newNotice.tb_dat = response.data.tb_dat
        newNotice.comment_count = 0
        notices.value = [newNotice, ...notices.value]
        showForm.value = false
        formTitle.value = ''
        formContent.value = ''
        updateGrid()
      } catch (error) {
        console.error('Failed to save the new notice', error)
      }
    }

    // 일반문의 수정 확인
    const confirmUpdateNotice = () => {
      if (confirm('일반문의을 수정하시겠습니까?')) {
        updateNotice()
      }
    }

    // 일반문의 수정
    const updateNotice = async () => {
      try {
        const updatedNotice = { tb_tit: formTitle.value, tb_txt: formContent.value, tb_nam: store.state.usrInfo.usrName }
        const response = await axios.put(`/api/updateNotice/${selectedNotice.value.tb_num}`, updatedNotice)
        const index = notices.value.findIndex(notice => notice.tb_num === selectedNotice.value.tb_num)
        if (index !== -1) {
          notices.value[index] = { ...notices.value[index], ...response.data }
        }
        showForm.value = false
        formTitle.value = ''
        formContent.value = ''
        selectedNotice.value = null
        updateGrid()
      } catch (error) {
        console.error('Failed to update the notice', error)
      }
    }

    // 일반문의 삭제 확인
    const confirmDeleteNotice = () => {
      if (confirm('일반문의을 삭제하시겠습니까?')) {
        deleteNotice()
      }
    }

    // 일반문의 삭제
    const deleteNotice = async () => {
      try {
        await axios.delete(`/api/deleteNotice/${selectedNotice.value.tb_num}`)
        notices.value = notices.value.filter(notice => notice.tb_num !== selectedNotice.value.tb_num)
        showForm.value = false
        formTitle.value = ''
        formContent.value = ''
        selectedNotice.value = null
        updateGrid()
      } catch (error) {
        console.error('Failed to delete the notice', error)
      }
    }

    // 그리드 업데이트
    const updateGrid = () => {
      if (gridApi.value) {
        gridApi.value.setRowData([...notices.value])
        gridApi.value.refreshCells({ force: true })
      }
    }

    // 일반문의의 댓글 로드
    const loadComments = async (noticeId) => {
      try {
        const response = await axios.get(`/api/notices/${noticeId}/comments`)
        comments.value = response.data
      } catch (error) {
        console.error('Failed to load comments', error)
      }
    }

    // 댓글 추가
    const addComment = async () => {
      try {
        const newComment = { com_txt: commentContent.value, com_nam: store.state.usrInfo.usrName, usrId: store.state.usrInfo.usrId }
        const response = await axios.post(`/api/notices/${selectedNotice.value.tb_num}/comments`, newComment)
        const addedComment = response.data
        comments.value.push(addedComment)
        commentContent.value = ''
        const notice = notices.value.find(notice => notice.tb_num === selectedNotice.value.tb_num)
        if (notice) {
          notice.comment_count += 1
          updateGrid()
        }
      } catch (error) {
        console.error('Failed to add comment', error)
      }
    }

    // 댓글 삭제 확인
    const confirmDeleteComment = (commentId) => {
      if (confirm('댓글을 삭제하시겠습니까?')) {
        deleteComment(commentId)
      }
    }

    // 댓글 삭제
    const deleteComment = async (commentId) => {
      try {
        await axios.delete(`/api/comments/${commentId}`)
        comments.value = comments.value.filter(comment => comment.com_id !== commentId)
        const notice = notices.value.find(notice => notice.tb_num === selectedNotice.value.tb_num)
        if (notice) {
          notice.comment_count -= 1
          updateGrid()
        }
      } catch (error) {
        console.error('Failed to delete comment', error)
      }
    }

    return {
      gridOptions,
      columnDefs,
      notices,
      selectedNotice,
      formTitle,
      formContent,
      showForm,
      addNotice,
      saveNewNotice,
      confirmUpdateNotice,
      updateNotice,
      confirmDeleteNotice,
      deleteNotice,
      comments,
      commentContent,
      addComment,
      confirmDeleteComment,
      deleteComment,
      onFirstDataRendered
    }
  }
}
</script>

<style scoped>
.detail-view {
  border-left: 1px solid #ccc;
}
.comment {
  border: 1px solid #e2e8f0;
}
.reply {
  border: 1px solid #cbd5e1;
}
</style>
